import React, {Component} from 'react';
import {Social_media_faceboo_popup} from './social_media.js';
import {Sync_facebook_data_play,Sync_facebook_data,Sync_facebook_data_you_video,Sync_facebook_data_you_audio,Sync_facebook_data_play_video,Sync_facebook_data_play_audio,Upload_stage_plot,STYLE,Loader,deleteFile} from './Sync_data.js';
import Swal from 'sweetalert2';
import {mongodb_url,header_config} from '../../config/Index.jsx';
import axios from 'axios';
import $ from 'jquery';
import 'dropify';
import './dropify.min.css';
import {Header,client,db} from "../Header/Header.js";
import jQuery from 'jquery';
import YouTube from 'react-youtube';
import { Player } from 'video-react';
import "./video-react.css";
import AudioPlayer from "react-h5-audio-player";
import {Image_upload} from '../UserDashboard/Image_upload.js';

const user=JSON.parse(localStorage.getItem('user'));
const page=JSON.parse(localStorage.getItem('page'));
const selectedPage=JSON.parse(localStorage.getItem('selectedPage'));
const onboarding_data=JSON.parse(localStorage.getItem('onboarding_data'));


var array_data=[];
var images_data='';
let play_data='';
export class Sync_upload_play_section extends React.Component{
	constructor(){
		super();
		this.state={
				play_checked: false,
				display_loader:STYLE.infoDisplayNone,
				popup:STYLE.infoDisplayNone,
				display_images:'',
				play_video_preview:STYLE.infoDisplayNone,
				play_youtube_url:'',
				youtube_popup:STYLE.infoDisplayNone,
				video_src:"",
			    upload_video:STYLE.infoDisplay,
				video_status:STYLE.infoDisplayNone,
				play_default_player:STYLE.infoDisplay,
				play_change_player:STYLE.infoDisplayNone,
				audio_status:"audio_play_display_none",
				upload_audio:STYLE.infoDisplay,
				audio_src:"",
				dis_or_not:STYLE.infoSyncDisabled
		}
	}
	
	componentDidMount (){
		 $('.dropify').dropify();
	}
	componentWillMount (){
		 $('.dropify').dropify();
	}
	componentUpdateMount (){
		 $('.dropify').dropify();
	}
	
	upload_play_section_file = (ev) => {
		let images_data=[];
		var formData = new FormData();
		var imagefile = ev.target;
		formData.append("file", imagefile.files[0]);
		formData.append("folder_name", 'playitlive/images');
		formData.append("file_type", 'image');
		if(imagefile.files.length>0) {
			jQuery('.loader').css('display','block');
			const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
			const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
			let update_image_index=ev.currentTarget.dataset.tag;
			if (extFile=="jpg" || extFile=="jpeg" || extFile=="png" || extFile=="gif"){
					let data=[];
					var date=new Date();
					var current_time=date.getTime();
					Image_upload (ev.target.files[0],'images/',current_time);
					//let updated_image_url=response.data.trim();
					//data.push(response.data.trim());
					images_data.push('https://playitmarkermediafiles.s3.amazonaws.com/'+'playitlive/images/'+current_time+'_'+ev.target.files[0].name);
					jQuery('.loader').css('display','none');
					if(localStorage.getItem('play_data')!==null){
						play_data=JSON.parse(localStorage.getItem('play_data'));
						if('play_images' in play_data){
							play_data['play_images']=play_data.play_images.concat(images_data);
						localStorage.setItem('play_data',JSON.stringify(play_data));
						} else {
							play_data['play_images']=images_data;
							localStorage.setItem('play_data',JSON.stringify(play_data));
						}
					} else {
						let play_images_data={'play_images': images_data};
						localStorage.setItem('play_data',JSON.stringify(play_images_data));
					}
			} else {
				alert("Only jpg/jpeg, gif and png files are allowed!");
			}
		}
	}
	
	
	closePopup =() =>  {
		this.setState({
			popup:STYLE.infoDisplayNone,
			youtube_popup:STYLE.infoDisplayNone,
		});
	}
	
	play_check_upload_section = (e) => {
		if(e.target.checked===true){
			this.setState({
				play_checked:!this.state.play_checked,
				dis_or_not:STYLE.infoSync
			})
		} else {
			this.setState({
				play_checked:!this.state.play_checked,
				dis_or_not:STYLE.infoSyncDisabled
			})
		}
		
	}
	render(){
		var data=[];
		const opts = {
		  height: '390',
		  width: '640',
		};
		const facebook_images_data=JSON.parse(localStorage.getItem('facebook_pages_images'));
		const mongodb_data=JSON.parse(localStorage.getItem('play_data'));
		if(mongodb_data!==null){
			images_data=("play_images" in mongodb_data) ? mongodb_data.play_images : "[]";
		} 
		for(var key=0;key<4;key++) {
			data.push (
				<div key={key} className="add_img_block col-xs-6 cstm-plr">
					<input data-tag={key} accept="image/*" onChange={this.upload_play_section_file} type="file" name="image" id="input-file-now" className="dropify"   />
				</div>
			)
		}
		return (
			<div>
				
					<div className="question-icon question-icon-3 tooltips">
						<span className="tooltiptext">Check agreement before Uploading </span>
							<img src="images/question.png" />
						</div>
					<div >
						
						<div className="upload_socila_icon">
							<ul>
								<li>
									<a onClick={this.facebook_sync_images_plays}> <img src="images/facebook-yello.png"/></a>
								</li>
								<li>
									<a onClick={this.play_youtube_popup}> <img src="images/youtub-yello.png"/></a>
								</li>
								<li>
									<a href="#"> <img src="images/cloud-icon.png"/></a>
								</li>
							</ul>
						</div>
						<div id="myModal" className="modal2" style={this.state.popup}>
							<div className="modal-content2">
								<span className="close1" onClick={this.closePopup}>&times;</span>
								<div className="row fb_popup">
									{this.state.display_images}
								</div>
								<div style={STYLE.infoFloat}>
									<button className="btn btn-primary btn-lg click_but1">Click to Add Images</button>
								</div>
							</div>
						</div>
						<div id="play_youtube" className="play_youtube" style={this.state.youtube_popup}>
							<div className="modal-content_play_youtube">
								<span className="close1" onClick={this.closePopup}>&times;</span>
								<br/><br/>
								<div className="row fb_popup">
									<div className="input-group">
									  <span className="input-group-addon"><i className="fa  fa-youtube"></i></span>
									  <input id="play_url"  type="url" onChange={this.play_youtube_field} className="form-control" name="youtube_url_play" placeholder="Please enter video url only" />
									</div>
								</div>
								<div className="col-md-6" style={this.state.play_video_preview}>
								<YouTube
									videoId={this.state.play_youtube_url}
									opts={opts}
									onReady={this._onReady}
								/>
								</div>
								
								<br/><br/>
								<div style={STYLE.infoFloat}>
									<button className="btn btn-primary btn-lg" onClick={this.add_play_youtube_video}>Click to Add Youtube Video</button>
								</div>
							</div>
						</div>
						<div className="add_img_block_size">
							<div className="add_img_block_top mb-10 play_data">
								<div>{Loader(this.state.display_loader)}	
									 {data} 
								</div>
							</div>   
						</div>
						<div className="add_img_block_size_2 mb-10" >
							<div className="add_img_block_2 col-xs-12 video_dropify" style={this.state.upload_video}>
								<input accept="video/*" onChange={this.upload_you_section_video} type="file" name="image" id="input-file-now" className="dropify"   />
							</div>
							<div style={this.state.play_default_player}>
								<Player style={this.state.video_status} preload="auto" playsInline src={this.state.video_src} /> 
							</div>
							<div style={this.state.play_change_player}>
								<YouTube videoId={this.state.youtube_url_play} opts={opts} onReady={this._onReady}  />
							</div>
						</div>
						<div className="add_img_block_size_3">
							<div className="add_img_block_3 col-xs-12 video_dropify audio_drfy" style={this.state.upload_audio}>
								<input accept="audio/*" onChange={this.upload_you_section_audio} type="file" name="image" id="input-file-now" className="dropify"   />
							</div>
							<AudioPlayer src={this.state.audio_src} className={this.state.audio_status}/>
						</div>
						</div>
						
				</div>	
			
		)
	}
	
	facebook_sync_images_plays = () => {
		var self = this;
		jQuery('.modal2').css('display','none');
		jQuery('.loader').css('display','block');
		const pages=JSON.parse(localStorage.getItem('page'));
		const selectedpageindex = JSON.parse(localStorage.getItem('selectedPage'));
		axios.get('https://graph.facebook.com/'+pages.accounts.data[selectedpageindex].id+'/photos/?fields=images,width=150,height=150&access_token='+pages.accounts.data[selectedpageindex].access_token).then(function (response) {
		   if(response.data!== null){
				localStorage.setItem('facebook_pages_images',JSON.stringify(response.data));
				jQuery('.modal2').css('display','block');
				jQuery('.loader').css('display','none');
				self.setState({
					popup:STYLE.infoDisplay
				})
				let facebook_pages_images=JSON.parse(localStorage.getItem('facebook_pages_images'));
				self.setState({display_images:facebook_pages_images.data.map((item,key)=>{
					return(
						<div className="col-md-2 custom_all_fb_img">
						<img src={item.images[0].source} value={key} />
						</div>
					)
					})
				});
				
				jQuery('.custom_all_fb_img').each(function(){
					jQuery(this).on('click',function(){
						if(jQuery(this).hasClass('selected')){
							jQuery(this).removeClass('selected');
							array_data.pop(jQuery(this).find('img').attr('src'));
						} else {
							array_data.push(jQuery(this).find('img').attr('src'));
							jQuery(this).addClass('selected');
						}
					})
				})
				jQuery('.click_but1').on('click',function(){
					if(images_data.length>0){
						var all_data=array_data.concat(images_data);
					} else {
						var all_data=array_data;
					}
					
					jQuery('.play_data').find('.cstm-plr').each(function(){
						jQuery(this).remove();
					})
					 $('.dropify').dropify();
					for(var i=4;i>=0;i--){
						
						if(i<4){
							jQuery('.play_data').find('.loader').after('<div  key="'+i+'" class="add_img_block col-xs-6 cstm-plr"><div class="dropify-wrapper has-preview"><div class="dropify-message"><span class="file-icon"></span><p>Drag and drop a file here or click</p><p class="dropify-error">Ooops, something wrong happended.</p></div><div class="dropify-loader" style="display: none;"></div><div class="dropify-errors-container"><ul></ul></div><div class="dropify-wrapper has-preview"><div class="dropify-message"><span class="file-icon"></span> <p>Drag and drop a file here or click</p><p class="dropify-error">Ooops, something wrong happended.</p></div><div class="dropify-loader" style="display: none;"></div><div class="dropify-errors-container"><ul></ul></div><input data-tag="0" accept="image/*" type="file" name="image" id="input-file-now" class="dropify" data-default-file="'+((array_data[i]!==undefined) ? array_data[i] : '' )+'"><button type="button" class="dropify-clear"><img src="images/close.png"></button><div class="dropify-preview" style="display: block;"><span class="dropify-render"><img src="'+((array_data[i]!==undefined) ? array_data[i] : '' )+'"></span><div class="dropify-infos"><div class="dropify-infos-inner"><p class="dropify-filename"><span class="dropify-filename-inner">1562313440.jpg</span></p><p class="dropify-infos-message">Drag and drop or click to replace</p></div></div></div></div></div></div>');
						}
					}
					console.log(all_data);
					jQuery('.modal2').css('display','none');
					if(localStorage.getItem('play_data')!==null){
						play_data=JSON.parse(localStorage.getItem('play_data'));
						if('play_images' in play_data){
							play_data['play_images']=play_data.play_images.concat(array_data);
						localStorage.setItem('play_data',JSON.stringify(play_data));
						} else {
							play_data['play_images']=array_data;
							localStorage.setItem('play_data',JSON.stringify(play_data));
						}
					} else {
						let play_images_data={'play_images': array_data};
						localStorage.setItem('play_data',JSON.stringify(play_images_data));
					}
					
					
					
					/*
					var formData = new FormData();
					formData.append("user_id", user.id);
					formData.append("page_id", page.accounts.data[selectedPage].id);
					formData.append('play_images',all_data.toString());
					jQuery('.loader').css('display','block');
					axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
						self.setState({
							display_loader:STYLE.infoDisplayNone
						})
						jQuery('.loader').css('display','none');
					})*/
				})
				
		   }
		}).catch(error=>{
			if(error.response.status===400){
					sessionStorage.setItem('check_session',false);
					window.location.href="/";
			}
		})
	}
	
	play_youtube_popup = () => {
		this.setState({youtube_popup:STYLE.infoDisplay});
	}
	
	play_youtube_field = (e) => {
		console.log(this.isValidURL(e.target.value));
		if(this.isValidURL(e.target.value)===true && this.isValidURL(e.target.value)!==''){
			var url = new URL(e.target.value);
			var param = url.searchParams.get("v");
			console.log(param);
			this.setState({
				play_video_preview:STYLE.infoDisplay,
				play_youtube_url:param
			});
		} else {
			Swal.fire('Error', 'Invalid or empty Url', 'error');
			this.setState({
				play_video_preview:STYLE.infoDisplayNone,
			});
		}
	}
	
	isValidURL = (u) =>{
	  var elm;
	  if(!elm){
		elm = document.createElement('input');
		elm.setAttribute('type', 'url');
	  }
	  if(u!==''){
		elm.value = u;
		return elm.validity.valid;	
	  } else {
		  return false;
	  }
	  
	}
	
	add_play_youtube_video = () =>{
		var current_url_value=document.getElementById('play_url').value;
		console.log(current_url_value);
		if(this.isValidURL(current_url_value)===true && this.isValidURL(current_url_value)!==''){
			if(localStorage.getItem('play_data')!==null){
				let play_data=JSON.parse(localStorage.getItem('play_data'));
				play_data['play_videos']=current_url_value;
				localStorage.setItem('play_data',JSON.stringify(play_data));
			} else {
				let play_data={'play_videos':current_url_value};
				localStorage.setItem('play_data',JSON.stringify(play_data));
			}
			this.setState({
				play_change_player:STYLE.infoDisplay,
				play_default_player:STYLE.infoDisplayNone,
				upload_video:STYLE.infoDisplayNone,
				youtube_popup:STYLE.infoDisplayNone,
				youtube_url_play:this.state.play_youtube_url
			});
		} else {
			Swal.fire('Error', 'Invalid or empty Url', 'error');
			this.setState({
				play_video_preview:STYLE.infoDisplayNone,
			});
		}
	}
	
	upload_you_section_video = (ev) => {
				var formData = new FormData();
				var imagefile = ev.target;
				formData.append("file", imagefile.files[0]);
				formData.append("folder_name", 'playitlive/video');
				formData.append("file_type", 'video');
				if(imagefile.files.length>0) {
				  var self=this;
				  jQuery('.loader').css('display','block');
				  const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
				  const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
				  if (extFile=="m4v" || extFile=="avi" || extFile=="mpg" || extFile=="mp4" || extFile=="mpeg" || extFile=="wmv"){
						var date=new Date();
						var current_time=date.getTime();
						Image_upload (ev.target.files[0],'video/',current_time);
						let data='https://playitmarkermediafiles.s3.amazonaws.com/'+'playitlive/video/'+current_time+'_'+ev.target.files[0].name;
						
						if(localStorage.getItem('play_data')!==null){
							let play_data=JSON.parse(localStorage.getItem('play_data'));
							play_data['play_videos']=data;
							localStorage.setItem('play_data',JSON.stringify(play_data));
						} else {
							let play_data={'play_videos':data};
							localStorage.setItem('play_data',JSON.stringify(play_data));
						}
							self.setState({video_src:data});
						/*
						if(response.data!==''){
							  client.auth.loginWithCredential(new AnonymousCredential()).then(user => {
									db.collection('onboarding_data').updateOne({owner_id: client.auth.user.id}, {$set:{play_video:data}}, {upsert:true})
							  }).then(() => 
									db.collection('onboarding_data').findOne({owner_id: client.auth.user.id}, { limit: 1 })
							  ).then(result => {
									localStorage.setItem('onboarding_data',JSON.stringify(result));
									self.setState({display_loader:STYLE.infoDisplayNone})
							  })
						}*/
						jQuery('.loader').css('display','none');
						self.setState({
							upload_video:STYLE.infoDisplayNone,
							video_status:STYLE.infoDisplay
						})
					
				  } else {
					  alert("Only mpg/mpeg, mp4, avi, wmv and m4v files are allowed!");
				  }
					
				}
			}
			
			
			upload_you_section_audio = (ev) => {
			var formData = new FormData();
			var imagefile = ev.target;
			formData.append("file", imagefile.files[0]);
			formData.append("folder_name", 'playitlive/audio');
			formData.append("file_type", 'audio');
			if(imagefile.files.length>0) {
				jQuery('.loader').css('display','block');
				var self=this;
				const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
				const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
				if (extFile=="mp3" || extFile=="mp4"){
						
						var date=new Date();
						var current_time=date.getTime();
						Image_upload (ev.target.files[0],'audio/',current_time);
						let data='https://playitmarkermediafiles.s3.amazonaws.com/'+'playitlive/audio/'+current_time+'_'+ev.target.files[0].name;
						
						//let data=response.data.trim()
						if(localStorage.getItem('play_data')!==null){
							let play_data=JSON.parse(localStorage.getItem('play_data'));
							play_data['play_audio']=data;
							localStorage.setItem('play_data',JSON.stringify(play_data));
						} else {
							let play_data={'play_audio':data};
							localStorage.setItem('play_data',JSON.stringify(play_data));
						}
						self.setState({audio_src:data});
						/*
						if(response.data!==''){
							client.auth.loginWithCredential(new AnonymousCredential()).then(user => {
								db.collection('onboarding_data').updateOne({owner_id: client.auth.user.id}, {$set:{play_audio:data}}, {upsert:true})
							}).then(() => 
								db.collection('onboarding_data').findOne({owner_id: client.auth.user.id}, { limit: 1 })
							).then(result => {
							localStorage.setItem('onboarding_data',JSON.stringify(result));
							
						  })
						}*/
						jQuery('.loader').css('display','none');
						self.setState({
							audio_status:"audio_play_display",
							upload_audio:STYLE.infoDisplayNone,
						})
					
				  } else {
					  alert("Only mp3 and mp4 files are allowed!");
				  }
				}
			}
	/*
		<div key={key} className="add_img_block col-xs-6 cstm-plr">
							<input data-tag={key} accept="image/*" onChange={this.upload_you_section_file} type="file" name="image" id="input-file-now" className="dropify"  data-default-file={images_data[key]} />
							</div>
	*/
}