import React, {Component} from 'react';
import {Social_media_faceboo_popup} from './social_media.js';
import {Sync_facebook_data_play,Sync_facebook_data,Sync_facebook_data_you_video,Sync_facebook_data_you_audio,Sync_facebook_data_play_video,Sync_facebook_data_play_audio,Upload_stage_plot,STYLE,Loader,deleteFile} from './Sync_data.js';
import Swal from 'sweetalert2';
import {mongodb_url,header_config} from '../../config/Index.jsx';
import axios from 'axios';
import $ from 'jquery';
import 'dropify';
import './dropify.min.css';
import {Header,client,db} from "../Header/Header.js";
import jQuery from 'jquery';
import { Player } from 'video-react';
import "./video-react.css";
import YouTube from 'react-youtube';
import AudioPlayer from "react-h5-audio-player";
import {Image_upload} from '../UserDashboard/Image_upload.js';
import soundcloudDl from 'soundcloud-dl';

const user=JSON.parse(localStorage.getItem('user'));
const page=JSON.parse(localStorage.getItem('page'));
const selectedPage=JSON.parse(localStorage.getItem('selectedPage'));
const onboarding_data=JSON.parse(localStorage.getItem('onboarding_data'));


var data=[];
var array_data=[];
var video_data=[];
var images_data='';
var react_funcation="";
var you_images=[];
export class Sync_upload_you_section extends React.Component{
	constructor(){
		super();
		this.state={
				you_checked: false,
				display_loader:STYLE.infoDisplayNone,
				popup:STYLE.infoDisplayNone,
				display_images:'',
				youtube_popup:STYLE.infoDisplayNone,
				soundcloud:STYLE.infoDisplayNone,
				current_url:'',
				video_preview:STYLE.infoDisplayNone,
				youtube_url:'',
				default_player:STYLE.infoDisplay,
				change_player:STYLE.infoDisplayNone,
				dis_or_not:STYLE.infoSyncDisabled,
				dropify_section:[],
				youtube_div:STYLE.infoDisplayNone
		}
		//this.dropify_section_container();
	}
	
	componentDidMount (){
		$('.dropify').dropify();
	}
	componentWillMount (){
		$('.dropify').dropify();
	}
	componentUpdateMount (){
		 $('.dropify').dropify();
		 
	}
	
	upload_you_section_file = (ev) => {
		var formData = new FormData();
		var imagefile = ev.target;
		formData.append("file", imagefile.files[0]);
		formData.append("folder_name", 'playitlive/images');
		formData.append("file_type", 'image');
		if(imagefile.files.length>0) {
			jQuery('.loader').css('display','block');
			const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
			const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
					
			//let update_image_index=ev.currentTarget.dataset.tag;
			var update_image_index=[];
			if (extFile=="jpg" || extFile=="jpeg" || extFile=="png" || extFile=="gif"){
				var date=new Date();
				var current_time=date.getTime();
				Image_upload (ev.target.files[0],'images/',current_time);
				var formData = new FormData();
				formData.append("user_id", user.id);
				formData.append("page_id", page.accounts.data[selectedPage].id);
				
				var image_urls='https://playitmarkermediafiles.s3.amazonaws.com/'+'playitlive/images/'+current_time+'_'+ev.target.files[0].name;
				
				if(localStorage.getItem('onboarding_data')!==null && onboarding_data.you_images!==null){
					if('you_images' in onboarding_data){
							you_images=JSON.parse(onboarding_data.you_images);
							you_images.push(image_urls);
							formData.append('you_images',JSON.stringify(you_images));
							axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
								axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
										localStorage.setItem('onboarding_data',JSON.stringify(result.data));
									}).catch(err => {
										window.location.href="/AfterLogin";
									});
								jQuery('.loader').css('display','none');
							})
							console.log("hello"+JSON.stringify(you_images));
						} else {
							you_images.push(image_urls);
							formData.append('you_images',JSON.stringify(you_images))
							formData.append('you_images',you_images);
							axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
								axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
										localStorage.setItem('onboarding_data',JSON.stringify(result.data));
									}).catch(err => {
										window.location.href="/AfterLogin";
									});
								jQuery('.loader').css('display','none');
							})
							console.log("Bye"+JSON.stringify(you_images));
						}
				} else {
					you_images.push(image_urls);
					formData.append('you_images',you_images);
					axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
						axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
							localStorage.setItem('onboarding_data',JSON.stringify(result));
							jQuery('.loader').css('display','none');
						});
					})
				}	
			} else {
				alert("Only jpg/jpeg, gif and png files are allowed!");
			}
		}
	}
	
	
	
	you_check_upload_section = (e) => {
		if(e.target.checked===true){
			this.setState({
				you_checked:!this.state.you_checked,
				dis_or_not:STYLE.infoSync
			})
		} else {
			this.setState({
				you_checked:!this.state.you_checked,
				dis_or_not:STYLE.infoSyncDisabled
			})
		}
		
	}
	
	closePopup =() =>  {
		this.setState({
			popup:STYLE.infoDisplayNone,
			youtube_popup:STYLE.infoDisplayNone,
			soundcloud:STYLE.infoDisplayNone
		});
	}
	
	 _onReady(event) {
		// access to player in all event handlers via event.target
		event.target.pauseVideo();
	  }
	 
	soundcloud_search= () => {
		const header_config = {
			headers:{
				"Access-Control-Allow-Origin":"*"
			} 
		}
		let url=document.getElementById('soundcloud_url').value;
		console.log(url);
		if(url!==null){
			soundcloudDl.getSongDlByURL(url,header_config).then(function(song){
				console.log(song)
			});
		}
	}
	
	
	 check_you_video_url (){
		
		 const opts = {
		  height: '390',
		  width: '640',
		};
		video_data.push(<div>
				<div className="add_img_block_size_2 mb-10 upload_video">
					<div className="add_img_block_2 col-xs-12 video_dropify ">
						<input accept="video/*" onChange={this.upload_you_section_video} type="file" name="image" id="input-file-now" className="dropify"  />
					</div>
				</div>
		</div>)
		return video_data;
	}
	
	dropify_section_container = () => {
		
	}
	
	render(){
		const opts = {
		  height: '390',
		  width: '640',
		};
		react_funcation=this.upload_you_section_file;
		const facebook_images_data=JSON.parse(localStorage.getItem('facebook_pages_images'));
				const mongodb_data=JSON.parse(localStorage.getItem('onboarding_data'));
				if(mongodb_data!==null){
					images_data=("you_images" in mongodb_data) ? mongodb_data.you_images.substr(1).split(',') : "";
				}
				data=[];
				for(var key=0;key<4;key++) {
					data.push (
							<div className="add_img_block col-xs-6 cstm-plr">
								<input data-tag={key} accept="image/*" onChange={this.upload_you_section_file} type="file" name="image" id="input-file-now" className="dropify"  />
							</div>
						
						)
				}
		return (
			<div>
					<div className="question-icon question-icon-3 tooltips">
						<span className="tooltiptext">Check agreement before Uploading </span>
						<img src="images/question.png" />
					</div>
					<div >
					<div className="upload_socila_icon">
						<ul>
							<li>
								<a onClick={this.facebook_sync_images}> <img src="images/facebook-yello.png"/></a>
							</li>
							<li>
								<a onClick={this.youtube}> <img src="images/youtub-yello.png"/></a>
							</li>
							<li>
								<a onClick={this.soundcloud}> <img src="images/cloud-icon.png"/></a>
							</li>
						</ul>
					</div>
					<div id="myModal" className="modal1" style={this.state.popup}>
						<div className="modal-content1">
							<span className="close1" onClick={this.closePopup}>&times;</span>
							<div className="row fb_popup">
								{this.state.display_images}
							</div>
							<div style={STYLE.infoFloat}>
								<button className="btn btn-primary btn-lg click_but">Click to Add Images</button>
							</div>
						</div>
					</div>
					<div id="you_youtube" className="you_youtube" style={this.state.youtube_popup}>
						<div className="modal-content_you_youtube">
							<span className="close1" onClick={this.closePopup}>&times;</span>
							<br/><br/>
							<div className="row fb_popup">
								<div className="input-group">
								  <span className="input-group-addon"><i className="fa  fa-youtube"></i></span>
								  <input id="url" onChange={this.youtube_field} type="url" className="form-control" name="youtube_url" placeholder="Please enter video url only" />
								</div>
							</div>
							<div className="col-md-6" style={this.state.video_preview}>
							<YouTube
								videoId={this.state.youtube_url}
								opts={opts}
								onReady={this._onReady}
							/>
							</div>
							
							<br/><br/>
							<div style={STYLE.infoFloat}>
								<button className="btn btn-primary btn-lg" onClick={this.add_youtube_video}>Click to Add Youtube Video</button>
							</div>
						</div>
					</div>
					
					<div id="soundcloud" className="modal1" style={this.state.soundcloud}>
						<div className="modal-content1">
							<span className="close1" onClick={this.closePopup}>&times;</span>
							<div className="row">
								<input type="url" id="soundcloud_url" className="form-control soundcloud_css" placeholder="Please enter soundcloud url only"/>
							</div>
							<div style={STYLE.infoFloat}>
								<button className="btn btn-primary btn-lg" onClick={this.soundcloud_search}>Click to Add Audio</button>
							</div>
						</div>
					</div>
					
					<div className="add_img_block_size">
						<div className="add_img_block_top mb-10 you_data">
							<div>{Loader(this.state.display_loader)}	
								 {data} 
							</div>
						</div>   
					</div>
					
					{this.check_you_video_url()}
					<div className="add_img_block_size_2 mb-10" style={this.state.youtube_div}>
						<div className="add_img_block_2 col-xs-12 video_dropify" >
							<YouTube videoId={this.state.youtube_url} opts={opts} onReady={this._onReady}/>
						</div>
					</div>
					<div className="add_img_block_size_3">
						<div className="add_img_block_3 col-xs-12 video_dropify audio_drfy">
							 <input accept="audio/*" onChange={this.upload_you_section_audio} type="file" name="image" id="input-file-now" className="dropify"   />
						</div>
					</div>
					</div>
				</div>
				
		)
	}
	
	facebook_sync_images = () => {
		var self = this;
		
		jQuery('.loader').css('display','block');
		const pages=JSON.parse(localStorage.getItem('page'));
		const selectedpageindex = JSON.parse(localStorage.getItem('selectedPage'));
		axios.get('https://graph.facebook.com/'+pages.accounts.data[selectedpageindex].id+'/photos/?fields=images,width=150,height=150&access_token='+pages.accounts.data[selectedpageindex].access_token).then(function (response) {
		   if(response.data!== null){
				localStorage.setItem('facebook_pages_images',JSON.stringify(response.data));
				jQuery('.modal1').css('display','block');
				jQuery('#soundcloud').css('display','none');
				self.setState({
					popup:STYLE.infoDisplay,
				})
				jQuery('.loader').css('display','none');
				let facebook_pages_images=JSON.parse(localStorage.getItem('facebook_pages_images'));
				self.setState({display_images:facebook_pages_images.data.map((item,key)=>{
					return(
						<div className="col-md-2 custom_all_fb_img">
						<img src={item.images[0].source} value={key} />
						</div>
					)
					})
				});
				
				jQuery('.custom_all_fb_img').each(function(){
					jQuery(this).on('click',function(){
						if(jQuery(this).hasClass('selected')){
							jQuery(this).removeClass('selected');
							array_data.pop(jQuery(this).find('img').attr('src'));
						} else {
							array_data.push(jQuery(this).find('img').attr('src'));
							jQuery(this).addClass('selected');
						}
					})
				})
				jQuery('.click_but').on('click',function(){
					if(images_data.length>0){
						var all_data=array_data.concat(images_data);
					} else {
						var all_data=array_data;
					}
					
					jQuery('.you_data').find('.cstm-plr').each(function(){
						jQuery(this).remove();
					})
					$('.dropify').dropify();
					for(var i=4;i>=0;i--){
						console.log(all_data[i]);
						if(i<4){
							jQuery('.you_data').find('.loader').after('<div  key="'+i+'" class="add_img_block col-xs-6 cstm-plr"><div class="dropify-wrapper has-preview"><div class="dropify-message"><span class="file-icon"></span> <p>Drag and drop a file here or click</p><p class="dropify-error">Ooops, something wrong happended.</p></div><div class="dropify-loader" style="display: none;"></div><div class="dropify-errors-container"><ul></ul></div><input data-tag="0" accept="image/*" type="file" name="image" id="input-file-now" class="dropify" data-default-file="'+((array_data[i]!==undefined)? array_data[i] : '')+'"><button type="button" class="dropify-clear"><img src="images/close.png"></button><div class="dropify-preview" style="display: block;"><span class="dropify-render"><img src="'+((array_data[i]!==undefined)? array_data[i] : '')+'"></span><div class="dropify-infos"><div class="dropify-infos-inner"><p class="dropify-filename"><span class="dropify-filename-inner">1562313440.jpg</span></p><p class="dropify-infos-message">Drag and drop or click to replace</p></div></div></div></div></div></div>');
						}
					}
					jQuery('.modal1').css('display','none');
					
					var formData = new FormData();
					formData.append("user_id", user.id);
					formData.append("page_id", page.accounts.data[selectedPage].id);
					formData.append('you_images',all_data.toString());
					jQuery('.loader').css('display','block');
					axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
						jQuery('.loader').css('display','none');
					})
					jQuery('.loader').css('display','none');
				})
				
		   }
		}).catch(error=>{
			if(error.response.status===400){
					sessionStorage.setItem('check_session',false);
					window.location.href="/";
			}
		})
	}
	
	
	
	youtube = () => {
		this.setState({youtube_popup:STYLE.infoDisplay});
		console.log(this.state.youtube_popup);
	}
	soundcloud = () => {
		this.setState({soundcloud:STYLE.infoDisplay});
	}
	
	youtube_field = (e) => {
		console.log(this.isValidURL(e.target.value));
		if(this.isValidURL(e.target.value)===true && this.isValidURL(e.target.value)!==''){
			var url = new URL(e.target.value);
			var param = url.searchParams.get("v");
			console.log(param);
			this.setState({
				video_preview:STYLE.infoDisplay,
				youtube_url:param
			});
		} else {
			Swal.fire('Error', 'Invalid or empty Url', 'error');
			this.setState({
				video_preview:STYLE.infoDisplayNone,
			});
		}
	}
	isValidURL = (u) =>{
	  var elm;
	  if(!elm){
		elm = document.createElement('input');
		elm.setAttribute('type', 'url');
	  }
	  if(u!==''){
		elm.value = u;
		return elm.validity.valid;	
	  } else {
		  return false;
	  }
	  
	}
	
	add_youtube_video = () =>{
		var current_url_value=document.getElementById('url').value;
		console.log(current_url_value);
		if(this.isValidURL(current_url_value)===true && this.isValidURL(current_url_value)!==''){
			jQuery('.loader').css('display','inline');
			$('.upload_video').remove();
			this.setState({
				youtube_div:STYLE.infoDisplay
			})
			var self=this;
			var formData = new FormData();
			formData.append("user_id", user.id);
			formData.append("page_id", page.accounts.data[selectedPage].id);
			formData.append('you_video',current_url_value);
			axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
				axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(results => {
					localStorage.setItem('onboarding_data',JSON.stringify(results.data));
					jQuery('.loader').css('display','none');
					//jQuery('.you_youtube').css('display','none');
					self.setState({
						change_player:STYLE.infoDisplay,
						default_player:STYLE.infoDisplayNone,
						youtube_popup:STYLE.infoDisplayNone,
					})
				});
			});
		} else {
			Swal.fire('Error', 'Invalid or empty Url', 'error');
			this.setState({
				video_preview:STYLE.infoDisplayNone,
			});
		}
	}
	
	/*
		<div key={key} className="add_img_block col-xs-6 cstm-plr">
							<input data-tag={key} accept="image/*" onChange={this.upload_you_section_file} type="file" name="image" id="input-file-now" className="dropify"  data-default-file={images_data[key]} />
							</div>
	*/
	upload_you_section_video = (ev) => {
				var formData = new FormData();
				var imagefile = ev.target;
				formData.append("file", imagefile.files[0]);
				formData.append("folder_name", 'playitlive/video');
				formData.append("file_type", 'video');
				if(imagefile.files.length>0) {
				  jQuery('.loader').css('display','block');
				  const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
				  const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
				  var self=this;
				  if (extFile=="m4v" || extFile=="avi" || extFile=="mpg" || extFile=="mp4" || extFile=="mpeg" || extFile=="wmv"){
					  var date=new Date();
					  var current_time=date.getTime();
					  Image_upload (ev.target.files[0],'video/',current_time);
					  let video_url='https://playitmarkermediafiles.s3.amazonaws.com/'+'playitlive/video/'+current_time+'_'+ev.target.files[0].name;
					  
					  
					  
					 	var formData = new FormData();
						formData.append("user_id", user.id);
						formData.append("page_id", page.accounts.data[selectedPage].id);
						formData.append('you_video',video_url);
						axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
							axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
								localStorage.setItem('onboarding_data',JSON.stringify(result.data));
								jQuery('.loader').css('display','none');
							});
						});
				  } else {
					  alert("Only mpg/mpeg, mp4, avi, wmv and m4v files are allowed!");
				  }
					
				}
			}
			/******************* View Video Section***********************/
		
			/************ Start section for audio upload and view ***********/		
			
	upload_you_section_audio = (ev) => {
		var formData = new FormData();
		var imagefile = ev.target;
		formData.append("file", imagefile.files[0]);
		formData.append("folder_name", 'playitlive/audio');
		formData.append("file_type", 'audio');
		if(imagefile.files.length>0) {
			var self=this;
			jQuery('.loader').css('display','block');
			const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
			const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
			if (extFile=="mp3" || extFile=="mp4"){
				var date=new Date();
				var current_time=date.getTime();
				Image_upload (ev.target.files[0],'audio/',current_time);
				let audio_url='https://playitmarkermediafiles.s3.amazonaws.com/'+'playitlive/audio/'+current_time+'_'+ev.target.files[0].name;
				var formData = new FormData();
				formData.append("user_id", user.id);
				formData.append("page_id", page.accounts.data[selectedPage].id);
				formData.append('you_audio',audio_url);
				axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
					axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
						localStorage.setItem('onboarding_data',JSON.stringify(result));
						jQuery('.loader').css('display','none');
					});
				});
			  } else {
				  alert("Only mp3 and mp4 files are allowed!");
			  }
			}
		}
			
			/************ End section for audio upload and view ***********/
}

