import React, { Component } from 'react';

import AWS from 'aws-sdk/global';
import S3 from 'aws-sdk/clients/s3';

export function Image_upload (data,folder,current_time) {
  var file = data;
  
  //console.log(data);
  if (file) {  
  AWS.config.update({
    "accessKeyId": "AKIAIVNBFW6STISZVZEQ",
    "secretAccessKey": "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",
    "region": "us-east-1"
  });
  var s3 = new AWS.S3();
  var params = {
    Bucket: 'playitmarkermediafiles',
    Key: "playitlive/"+folder+current_time+'_'+file.name,
    ContentType: file.type,
    Body: file,
    ACL: 'public-read',
  };    
  s3.putObject(params, function (err, res) {
	if (err) {
			alert(err);
	} else {
			return 1;
		}
	});
  } else {
	  return false;
  }
}


export function  Delete_image(file_name){
	AWS.config.update({
		"accessKeyId": "AKIAIVNBFW6STISZVZEQ",
		"secretAccessKey": "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",
		"region": "us-east-1"
	});
	const s3 = new AWS.S3();

	const params = {
			Bucket: 'playitmarkermediafiles',
			Key: file_name
	}
	try {
		s3.headObject(params).promise()
		console.log("File Found in S3")
		try {
			s3.deleteObject(params).promise()
			console.log("file deleted Successfully")
		}
		catch (err) {
			 console.log("ERROR in file Deleting : " + JSON.stringify(err))
		}
	} catch (err) {
			console.log("File not Found ERROR : " + err.code)
	}
}
