import jQuery from 'jquery';
import './jquery.hislide.css';
(function($) {
    var slide = function(ele,options) {
        var $ele = $(ele);
        var setting = {
        	 speed: 1000,
            interval: 2000,
            
        };
        $.extend(true, setting, options);
		var states = [
			{ zIndex: 1, width: 133, height: 140, top: 60, left: -22, $opacity: 1 },
			{ zIndex: 2, width: 160, height: 160, top: 49, left: 107, $opacity: 1 },
			{ zIndex: 3, width: 175, height: 180, top: 35, left: 265, $opacity: 1 },
			{ zIndex: 4, width: 200, height: 200, top: 25, left: 435, $opacity: 1 },
			{ zIndex: 3, width: 170, height: 180, top: 35, left: 625, $opacity: 1 },
			{ zIndex: 2, width: 200, height: 160, top: 49, left: 740, $opacity: 1 },
			{ zIndex: 1, width: 200, height: 140, top: 60, left: 900, $opacity: 1 }
		];
		var $lis = $ele.find('li');
        var timer = null;

       $ele.find('.hi-next').on('click', function() {
            next();
        });
        $ele.find('.hi-prev').on('click', function() {
            states.push(states.shift());
            move();
        });
        $ele.on('mouseenter', function() {
            clearInterval(timer);
            timer = null;
        }).on('mouseleave', function() {
            autoPlay();
        });

        move();
        autoPlay();
		function move() {
            $lis.each(function(index, element) {
				var state = states[index];
				$(element).css('zIndex', states[index]).finish().animate(state, setting.speed).find('img').css('opacity', '1');
            });
        }

        function next() {
            states.unshift(states.pop());
            move();
        }

        function autoPlay() {
            timer = setInterval(next, setting.interval);
        }
    }
		$.fn.hiSlide = function(options) {
        $(this).each(function(index, ele) {
            slide(ele,options);
        });
        return this;
    }
})(jQuery);
