	upload_slider_images = (ev) => {
		var formData = new FormData();
		var imagefile = ev.target;
		formData.append("file", imagefile.files[0]);
		formData.append("folder_name", 'playitlive/images');
		formData.append("file_type", 'image');
		if(imagefile.files.length>0) {
			$('.loader').css('display','block');
			const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
			const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
					
			//let update_image_index=ev.currentTarget.dataset.tag;
			var update_image_index=[];
			if (extFile=="jpg" || extFile=="jpeg" || extFile=="png" || extFile=="gif"){
				var date=new Date();
				var current_time=date.getTime();
				let current_tag=ev.currentTarget.dataset.tag;
				let current_src=ev.currentTarget.dataset.defaultFile;
				Image_upload (ev.target.files[0],'images/',current_time);
				var formData = new FormData();
				formData.append("user_id", user.id);
				formData.append("page_id", page.accounts.data[selectedPage].id);
				var image_urls='https://playitmarkermediafiles.s3.amazonaws.com/'+'playitlive/images/'+current_time+'_'+ev.target.files[0].name;
				
				if(current_src!==undefined && current_src!==''){
					const onboarding_data1=JSON.parse(localStorage.getItem('onboarding_data'));
					let you_images1=JSON.parse(onboarding_data1.you_images);
					you_images1[current_tag]=image_urls;
					
					// deleteing you images
					Delete_image(current_src.replace("https://playitmarkermediafiles.s3.amazonaws.com/", ""));
					formData.append('you_images',JSON.stringify(you_images1));
					axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
						axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
							localStorage.setItem('onboarding_data',JSON.stringify(result.data));
							setTimeout(function(){ window.location.reload(); }, 3000);
						}).catch(err => {
							window.location.href="/AfterLogin";
						});
						//$('.loader').css('display','none');
					})
				} else {
					axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
						update_image_index=("you_images" in result.data) ? JSON.parse(result.data.you_images) : "";
						console.log(update_image_index);
							if(update_image_index.length>0){
								update_image_index.push(image_urls);
								formData.append('you_images',JSON.stringify(update_image_index));
								axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
									axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
										localStorage.setItem('onboarding_data',JSON.stringify(result.data));
										setTimeout(function(){ window.location.reload(); }, 3000);
									}).catch(err => {
										window.location.href="/AfterLogin";
									});
									//$('.loader').css('display','none');
								})
								console.log("Hello"+(update_image_index));
							} else {
								var add_new_image=[];
								console.log(image_urls);
								console.log(add_new_image);
								add_new_image.push(image_urls)
								console.log(add_new_image);
								//return false;
								formData.append('you_images',JSON.stringify(add_new_image));
								axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
									axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
									localStorage.setItem('onboarding_data',JSON.stringify(result.data));
									setTimeout(function(){ window.location.reload(); }, 3000);
								}).catch(err => {
									window.location.href="/AfterLogin";
								});
									//$('.loader').css('display','none');
								})
								console.log("Bye"+(add_new_image));
							}
						})
					}
					
			} else {
				alert("Only jpg/jpeg, gif and png files are allowed!");
			}
		}
	}
