import React, { Component } from 'react';
import Spinner from 'react-spinner-material';
import graph from 'fb-react-sdk';
import {Header,client,db} from "../Header/Header.js";
import axios from 'axios';
import {mongodb_url,header_config} from '../../config/Index.jsx';
import { BrowserRouter, Route, NavLink, HashRouter, Switch,Redirect  } from "react-router-dom";

const STYLE = {
    infoWidth: {
        width: '100%'
    },
	infoColor:	{
		'color':'white',
		'fontSize':'1.2em',
		'textAlign' : 'center'
	},
	infoColortag: {
		'color':'red'
	},
	infoDisplayNone: {
		'display':'none',
	},
	infoDisplay : {
		'display':'block',
	}
};

export class AfterLogin extends Component {
	constructor(props) {
        super(props);
        this.state = {
            loading: 'true',
			display_loader:STYLE.infoDisplayNone,
        };
    }
	
	componentDidMount() {
		if(localStorage.getItem('current_page')!==undefined){
			const user=JSON.parse(localStorage.getItem('user'));
			const page=JSON.parse(localStorage.getItem("page"));
			page.accounts.data.map((item,key)=>{
				if(localStorage.getItem('current_page')===item.id){
					localStorage.setItem('selectedPage',key);
					this.user_data_api(key);
					
				}
			})
		}		
	}
	
	logout = () =>{
		if(localStorage.getItem('selectedPage')!==''){
			const user=JSON.parse(localStorage.getItem('user'));
			const page=JSON.parse(localStorage.getItem("page"));
			const selected_page_index=localStorage.getItem('selectedPage');
			if(sessionStorage.getItem('login_user_type')==='players'){
				localStorage.setItem('current_page',page.accounts.data[selected_page_index].id);
			}
			localStorage.removeItem('user');
			localStorage.removeItem('page');
			localStorage.removeItem('selectedPage');
		} else {
			localStorage.removeItem('user');
			localStorage.removeItem('page');
			localStorage.removeItem('selectedPage');
		}
		localStorage.removeItem('token');
		localStorage.removeItem('user_profile');
		localStorage.removeItem('selected_page_data');
		localStorage.removeItem('onboarding_data');
		localStorage.removeItem('fans_by_gender');
		localStorage.removeItem('fans_by_language');
		localStorage.removeItem('page_growth');
		localStorage.removeItem('players_data');
		localStorage.removeItem('fan_country_location');
		localStorage.removeItem('onboarding_play_data');
		sessionStorage.removeItem('login_user_type');
		//sessionStorage.removeItem('check_session');
		window.location.href="/";
	}
	
	switchToHome =() =>{
		localStorage.removeItem('user')
		localStorage.removeItem('page') 
		this.props.history.push('/')
	}
	switchToOnboarding =(e) =>{
		var self=this;
		self.setState({display_loader:STYLE.infoDisplay});
		localStorage.setItem('selectedPage',e.currentTarget.dataset.tag);
		const current_page_id=e.currentTarget.dataset.tag;
		this.user_data_api(current_page_id);
	}
	
	user_page_data_Store = (e) => {
		localStorage.setItem('selectedPage',e.currentTarget.dataset.tag);
		const user=JSON.parse(localStorage.getItem('user'));
		const selected_page_id=JSON.parse(localStorage.getItem("page"));
		graph.get(`${selected_page_id.accounts.data[e.currentTarget.dataset.tag].id}/feed/?access_token=${user.accessToken}`, function (err, res) {
				localStorage.setItem('selected_page_data',JSON.stringify(res));
		});
	}
	
	switchToOnboarding_host = (e)=> {
		localStorage.setItem('selectedPage',e.currentTarget.dataset.tag);
		this.props.history.push('/Onboarding_host');
	}
	
	
	
	render() {
		const location_url=this.switchToOnboarding;
		//const location_url=(sessionStorage.getItem('login_user_type')==="players") ? this.switchToOnboarding : this.switchToOnboarding_host;
		const { loading } = this.state;
		
		if (localStorage.getItem("page")!== '' || localStorage.getItem("user")!== '') {
				const Pages = ({pages=JSON.parse(localStorage.getItem("page")), Artist_selectPage=location_url,  selectPage=this.user_page_data_Store }) => {
				return pages!==null ? pages.accounts.data.map((item,k) => {
					if (sessionStorage.getItem('login_user_type')==="players") { 
						return (
								(item.category==="Artist" || item.category==="Public figure" || item.category==="Band" || item.category==="Musician/Band" || item.category==="DJ" || item.category==="Writer" || item.category==="Author" || item.category==="Journalist" || item.category==="Comedian" || item.category==="Dancer" || item.category==="Actor" || item.category==="Singer" || item.category==="Product/Service") ? <li key={k} data-tag={k} className="your_profile_img"  onClick={Artist_selectPage}><a href="javascript:void(0)" ><img 	src={'https://graph.facebook.com/'+item.id+'/picture?type=large'}/>
							   <span className="your_profile_name">{item.name}</span>
								</a></li> : ""
			
								) 
					} else {
						return (
							<li key={k} data-tag={k} className="your_profile_img"  onClick={Artist_selectPage}><a href="javascript:void(0)" ><img 	src={'https://graph.facebook.com/'+item.id+'/picture?type=large'}/>
							   <span className="your_profile_name">{item.name}</span>
								</a></li>
						)
					}
					}) : 
					<span style={STYLE.infoColor}>No pages Found <a style={STYLE.infoColortag} onClick={this.switchToHome}>Please Login Again</a></span>
			
		};
		const mobileViewPages = <div></div>
	
		return (
				<div>
					<div id="Homepage" className="hidden-xs">
						<section className="header-menu">
							<nav className="navbar navbar-inverse custom-navbar">
								<div className="container homepagetab-2">
									<div className="navbar-header">
										<button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
											<span className="icon-bar"></span>
											<span className="icon-bar"></span>
											<span className="icon-bar"></span>                        
										</button>
										<a className="navbar-brand playit_logo" href="#">
											<img src="images/logo-icon.png" />
										</a>
									</div>
									<div className="collapse navbar-collapse" id="myNavbar">
										<ul className="nav navbar-nav navbar-right custom-navbar-right">
											<li className="active login">
												<NavLink className="dropdown-toggle" data-toggle="dropdown" onClick={this.logout} to='#'>Logout</NavLink>
												 <ul className="dropdown-menu custom-dropdown-menu custom_profile_menu">
													<div className="click_your_profile">
													   <p >Click your profile to log in</p>
													</div>					        
													<div className="after-login-admin-1">
														<Pages  />
													</div>
												 </ul>
											</li>
										    <li><a href="#" className="dropdown-toggle">About</a></li>
										    <li><a href="#" className="dropdown-toggle">FAQ</a></li>
										    <li><a href="#" className="dropdown-toggle">Contact Us</a></li>
										</ul>
									</div>
								</div>
							</nav>
					</section>
				</div>
				<div id="Mobile-Homepage">
					<section className="header-menu">
						<nav className="navbar navbar-inverse custom-navbar">
							 <div className="container homepagetab-2">
								<div className="navbar-header">
								   <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar1">
									   <span className="icon-bar"></span>
									   <span className="icon-bar"></span>
									   <span className="icon-bar"></span>                        
								   </button>
								   <a className="navbar-brand playit_logo" href="#">
										<img src="images/mobile-logo.png"/>
								   </a>
								</div>
								<div className="collapse navbar-collapse" id="myNavbar1">
								   <ul className="nav navbar-nav navbar-right custom-navbar-right">
									  <li className="active login"> <a className="dropdown-toggle" href="#">Login</a></li>
									  <li><a href="#" className="dropdown-toggle">About</a></li>
									  <li><a href="#" className="dropdown-toggle">FAQ</a></li>
									  <li><a href="#" className="dropdown-toggle">Contact Us</a></li>
								   </ul>
								</div>
							 </div>
						  </nav>
					</section>
					<section className="banner-section">
						<div className="carousel-caption">
							<div className="custom-login-menu">
								<div className="tab-content custom-tab-content mobile-after-top">
									<div className="row">
										<div className="col-xs-12">
											<div className="click_your_profile">
												<p>Click your profile to log in</p>
											</div>
										</div>
											<Pages  />
									</div>
								</div>
						   </div>
						</div>
					</section>
				</div>
				<div className="container after-login-popup">
					<div className="modal fade" id="myModal" role="dialog">
						<div className="modal-dialog">
							<div className="modal-content">
								<div className="modal-header">
								  <button type="button" className="close" data-dismiss="modal"  ><img src="images/afclose.png"/></button>
							   
								</div>
							<div className="modal-body">
								  <p>Thank you for registering at <span className="aft_PlayIT-live">PlayITlive.app</span></p>
								  <p>Currently. The platform only allows for Artist And Cooperate registration.</p>
								  <p>But stay Tuned... We'll contact you soon to start producing your own events at <span className="aft_PlayIT-live">PlayITlive.app</span></p>
							</div>
						</div>
					</div>
				 </div>
			</div>
			<div className="loader" style={this.state.display_loader}>
				<img src="images/ezgif.com-video-to-gif.gif"/>
			</div>
		</div>
    );
   } else { window.location.href="/"; }
  } 
  
user_data_api = (page_index) => {
		var self=this;
		const user=JSON.parse(localStorage.getItem('user'));
		const selected_page_id=JSON.parse(localStorage.getItem("page"));
		const current_page_id=page_index;
		const url="https://graph.facebook.com/";
		
		/***** fetching facebook selecteted page gender wise data*****/
		
		axios.get(url+selected_page_id.accounts.data[current_page_id].id+"/insights/page_fans_gender_age/?access_token="+selected_page_id.accounts.data[current_page_id].access_token).then(response => {
			localStorage.setItem('fans_by_gender',JSON.stringify(response.data));
			
		}).catch(error=>{
			if(error.response.status===400){
					//sessionStorage.setItem('check_session',false);
					localStorage.clear();
					window.location.href="/";
			}
		})
		
		/***** fetching facebook selecteted page country wise data*****/
		
		axios.get(url+selected_page_id.accounts.data[current_page_id].id+"/insights/page_impressions_unique/?access_token="+selected_page_id.accounts.data[current_page_id].access_token).then(response=> {
			localStorage.setItem('page_growth',JSON.stringify(response));
		}).catch(error=>{
			if(error.response.status===400){
					//sessionStorage.setItem('check_session',false);
					localStorage.clear();
					window.location.href="/";
			}
		})
		
		/***** fetching facebook selecteted page country wise data*****/
		
		axios.get(url+selected_page_id.accounts.data[current_page_id].id+"/insights/page_fans_country/?access_token="+selected_page_id.accounts.data[current_page_id].access_token).then(response=> {
			localStorage.setItem('fan_country_location',JSON.stringify(response));
		}).catch(error=>{
			if(error.response.status===400){
					//sessionStorage.setItem('check_session',false);
					localStorage.clear();
					window.location.href="/";
			}
		})
		
		/***** fetching facebook selecteted page language wise data*****/
		
		axios.get(url+selected_page_id.accounts.data[current_page_id].id+"/insights/page_fans_locale/?access_token="+selected_page_id.accounts.data[current_page_id].access_token).then(response => {
			localStorage.setItem('fans_by_language',JSON.stringify(response.data));
			//self.setState({display_loader:STYLE.infoDisplayNone});
		}).catch(error=>{
			if(error.response.status===400){
					//sessionStorage.setItem('check_session',false);
					localStorage.clear();
					window.location.href="/";
			}
		})
		
		/***** fetching data of players data  *****/
		
		var formData = new FormData();
		formData.append("user_id", user.id);
		formData.append("page_id", selected_page_id.accounts.data[current_page_id].id);
		axios.post(mongodb_url+"get_onboarding_you_play",formData,header_config).then(results => {
			localStorage.setItem('players_data',JSON.stringify(results.data));	
		});
		
		/***** fetching data of user Onboarding  *****/
		var query={'page_id':selected_page_id.accounts.data[current_page_id].id};
		axios.post(mongodb_url+"get_login_user_data",query).then(result => {
			localStorage.setItem('onboarding_data',JSON.stringify(result.data));
			(sessionStorage.getItem('login_user_type')==="players") ? 
			 window.location.href="/Onboarding" : window.location.href="/About";
		}).catch(err => {
			sessionStorage.setItem('check_session',false);
			localStorage.clear();
			window.location.href="/";
		});
		localStorage.removeItem('current_page');
  }
  
}
export default AfterLogin;
