import React, { Component,ReactNode, SyntheticEvent,Children } from 'react';
import { NavLink} from "react-router-dom";
import ApiCalendar from 'react-google-calendar-api';
import { Calendar, momentLocalizer } from 'react-big-calendar'
import moment from 'moment';
import "react-big-calendar/lib/css/react-big-calendar.css";
import jQuery from 'jquery';
import {STYLE} from './Sync_data.js';
import './calendar.css';
import MicrosoftLogin from "react-microsoft-login";
import axios from 'axios';

const localizer = momentLocalizer(moment)
let data=[];

const onboarding_data=JSON.parse(localStorage.getItem('onboarding_data'));
const page=JSON.parse(localStorage.getItem('page'));
const selectedPage=JSON.parse(localStorage.getItem('selectedPage'));

export class You_Calendar extends React.Component{
	constructor(props) {
        super(props);
		this.state = {
			events: [
					  {
						start: new Date(),
						end: new Date(moment().add(1, "days")),
						title: "Some title"
					  }
					],
			calender_event:STYLE.infoSyncDisabled,
			calendar_close:STYLE.infoDisplayNone,
			calendar_open:STYLE.infoDisplay,
			selectedDay:new Date(),
			selectedDate:new Date()
			
		};
		
		this.handleItemClick = this.handleItemClick.bind(this);
		if(localStorage.getItem('google_calender-data')!==null){
			const calender_data=JSON.parse(localStorage.getItem('google_calender-data'));
			data=[];
			calender_data.map((item,key)=>{
				data.push({start:new Date(item.start.dateTime),end:new Date(item.end.dateTime),title:item.summary});
			})
		}
		
		if(localStorage.getItem('outlook_calendar_data')!==null){
			const outlook_calendar_data=JSON.parse(localStorage.getItem('outlook_calendar_data'));
			outlook_calendar_data.value.map((item,key)=>{
				data.push({start:new Date(item.start.dateTime),end:new Date(item.end.dateTime),title:item.subject});
			});
		}
			
		if(onboarding_data!==null){
			if('start_date' in onboarding_data){
				if('end_date' in onboarding_data){
					data.push({start:new Date(onboarding_data.start_date),end:new Date(onboarding_data.end_date),title:page.accounts.data[selectedPage].name});
				}
			}
		}
		this.state.events=data;
		this.state.calender_event=STYLE.infoSync;
		
	}
	
	componentDidMount(){
		jQuery('.rbc-btn-group').each(function(){
		if(jQuery(this).first().find('button').first().text()=="Today"){
			jQuery(this).first().find('button').first().remove();
		}
		})
		jQuery('.rbc-btn-group').each(function(){
			jQuery(this).first().find('button').each(function(key,item){
				if(jQuery(this).text()=="Back"){
					jQuery(this).text('');
					jQuery(this).html('<i class="fa fa-caret-left"></i>');
				} 
				if(jQuery(this).text()=="Next"){
					jQuery(this).text('');
					jQuery(this).html('<i class="fa fa-caret-right"></i>');
				}	
			})
			
		})
	}
	
	handleItemClick(event: SyntheticEvent<any>, name: string): void {
        if (name === 'sign-in') {
          ApiCalendar.handleAuthClick();
		  this.setState({
			  calender_event:STYLE.infoSync
		  })
        } else if (name === 'sign-out') {
          ApiCalendar.handleSignoutClick();
        }
      }
	  
	  
	  handleEvent = () => {
		  jQuery('.loader').show();
		  ApiCalendar.handleAuthClick();
		  ApiCalendar.listUpcomingEvents()
				.then(({result}: any) => {
					//console.log(result);
					localStorage.setItem('google_calender-data',JSON.stringify(result.items));
					data=[];
					result.items.map((item,key)=>{
						data.push({start:new Date(item.end.dateTime),end:new Date(item.start.dateTime),title:item.summary});
					})
					if('start_date' in onboarding_data){
						if('end_date' in onboarding_data){
							data.push({start:new Date(onboarding_data.end_date),end:new Date(onboarding_data.end_date),title:page.accounts.data[selectedPage].name});
						}
					}
					this.setState({
						events:data
					})
			jQuery('.loader').hide();
					//console.log(data);
	  })
	}
	
	onSelectEvent = (e) => {
		console.log(e);
	}
	
	authHandler = (err, result) => {
		//jQuery('.loader').show();
		if(result!==null){
			localStorage.setItem('outlook_access_token',JSON.stringify(result));
			if(localStorage.getItem('outlook_access_token')!==null){
				const outlook_data=JSON.parse(localStorage.getItem('outlook_access_token'));
				const header_config={
					headers: { 'Authorization': 'Bearer '+outlook_data.accessToken,
								'Accept' : 'application/json'
							 } 
				}
				axios.get('https://graph.microsoft.com/v1.0/me/events',header_config).then(response=>{
					localStorage.setItem('outlook_calendar_data',JSON.stringify(response.data));
					if(localStorage.getItem('outlook_calendar_data')!==null){
						const outlook_calendar_data=JSON.parse(localStorage.getItem('outlook_calendar_data'));
						//jQuery('.loader').hide();
						if(data.length>0){
							outlook_calendar_data.value.map((item,key)=>{
								data.push({start:new Date(item.start.dateTime),end:new Date(item.end.dateTime),title:item.subject});
							})
							this.setState({
								events:data
							})
						} else {
							data=[];
							outlook_calendar_data.value.map((item,key)=>{
								data.push({start:new Date(item.start.dateTime),end:new Date(item.end.dateTime),title:item.subject});
							})
							this.setState({
								events:data
							})
						}
					}
				}).catch(error=>{
					console.log(error);
				})
			}
		}
	}
	
	/*handleSelectEvent(event,target) {
		let obj = target.currentTarget;
		console.log(obj);
		console.log(event);
		var d = new Date(event);
		var weekday = new Array(7);
		weekday[0] = "Sun";
		weekday[1] = "Mon";
		weekday[2] = "Tues";
		weekday[3] = "Wed";
		weekday[4] = "Thu";
		weekday[5] = "Fri";
		weekday[6] = "Sat";
		var months = new Array('Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec');
		var dates=weekday[d.getDay()]+", "+ months[d.getMonth()]+" "+String(d.getDate()).padStart(2, '0')+" ,"+d.getFullYear();
		jQuery('.you_popup ').removeClass('custom_display_1');
		jQuery('.you_popup ').addClass('custom_display');
		jQuery('.you_popup ').find('#start_date').val(dates);
		jQuery('.you_popup ').find('#end_date').val(dates);
		jQuery('.you_popup ').find('#start_time').val((event.getHours()!='0') ? event.getHours() : '00' +':'+(event.getMinutes()!='0' ? event.getMinutes() : '00'));
		console.log(event.getHours()+':'+event.getMinutes());
		jQuery('.you_popup ').find('#end_time').val((event.getHours()!='0') ? event.getHours() : '00' +':'+(event.getMinutes()!='0' ? event.getMinutes() : '00'));
		jQuery('.you_popup ').find('.close_popup_button').on('click',function(){
			jQuery('.you_popup ').removeClass('custom_display');
			jQuery('.you_popup ').addClass('custom_display_1');
		})
		//obj.getElementsByTagName('strong')[0].click();
	}*/
	
	handleSelectEvent(events) {
		var event=events.currentTarget.dataset.tag; 
		var d = new Date(event);
		//console.log(d.getHours());
		//return false;
		var weekday = new Array(7);
		weekday[0] = "Sun";
		weekday[1] = "Mon";
		weekday[2] = "Tues";
		weekday[3] = "Wed";
		weekday[4] = "Thu";
		weekday[5] = "Fri";
		weekday[6] = "Sat";
		var months = new Array('Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec');
		var dates=weekday[d.getDay()]+", "+ months[d.getMonth()]+" "+String(d.getDate()).padStart(2, '0')+" ,"+d.getFullYear();
		jQuery('.you_popup ').removeClass('custom_display_1');
		jQuery('.you_popup ').addClass('custom_display');
		jQuery('.you_popup ').find('#start_date').val(dates);
		jQuery('.you_popup ').find('#end_date').val(dates);
		jQuery('.you_popup ').find('#start_time').val((d.getHours()!='0') ? d.getHours() : '00' +':'+(d.getMinutes()!='0' ? d.getMinutes() : '00'));
		console.log(d.getHours()+':'+d.getMinutes());
		jQuery('.you_popup ').find('#end_time').val((d.getHours()!='0') ? d.getHours() : '00' +':'+(d.getMinutes()!='0' ? d.getMinutes() : '00'));
		jQuery('.you_popup ').find('.close_popup_button').on('click',function(){
			jQuery('.you_popup ').removeClass('custom_display');
			jQuery('.you_popup ').addClass('custom_display_1');
		})
		//obj.getElementsByTagName('strong')[0].click();
	}
	
	handleView (event,target) {
		console.log(event);
		console.log(target);
	}
	
	
   render () {
		/*const ColoredDateCellWrapper = ({children, value}) =>
		React.cloneElement(Children.only(children), {
			style: {
			   backgroundColor: '#e9ae20',
			},
		});*/
	   console.log(this.state.events);
	   return (
			<div className="upload_media_calender">
				<div className="link_calender">
				   <ul>
					   <li><a href="#">sync with</a></li>
					   
					   <li><a href="#" onClick={(e) => this.handleItemClick(e, 'sign-in')}> <img src="images/Calendar-Play1.png"/></a></li>
					   
					   <li><a href="#" onClick={this.handleEvent} style={this.state.calender_event}> <img src="images/Calendar-Play2.png"/></a></li>
					   
					   <li className="tooltips">
						   <span className="tooltiptext microsoft_tooltip">
								<MicrosoftLogin
										clientId="c36e0aba-260c-47f6-af7e-f04618abcd77"
										authCallback={this.authHandler}
										graphScopes={["user.read","Calendars.Read"]}
										className="microsoft_button"
									/>
						   </span>
					   <a href="#"> <img src="images/Calendar-Play3.png"/></a></li>
					</ul>
				</div>
				<div className="custom_calender">
					<div className="brand-mak-calendar">
						<Calendar
						  localizer={localizer}
						  defaultDate={new Date()}
						  defaultView="month"
						  events={this.state.events}
						  //components={{dateEventWrapper: ColoredDateCellWrapper,}}
						  //onNavigate={this.handleSelectEvent} 
						  startAccessor="start"
						  endAccessor="end"
						  onView={this.handleView}
						  views={['month']}
						  components={{
								month: {
								  dateHeader: ({ date, label }) => {
								   let highlightDate =
								   this.state.events.find(event =>
										moment(date).isBetween(
										  moment(event.start),
										  moment(event.end),
										  null,
										  "[]"
										)
									) !== undefined ;
									return (
									  <h3 className={ highlightDate ? 'infoEventDesign'  : null } data-tag={date}  onClick={this.handleSelectEvent}>{label}</h3>
									);								  
								  }
								}
							  }}
						/>
						
					</div>
			    </div>
				<div className="calender_sub">
					<ul>
						<li><a href="#">Available</a></li>
						<li><a href="#">Partly Available</a></li>
						<li><a href="#">not Available </a></li>
					</ul>
				</div>
				</div>
	   )
   }
}



