import React, {Component} from 'react';
import {Header,client,db} from "../Header/Header.js";
import S3FileUpload from 'react-s3';
import 'dropify';
import $ from 'jquery';
import axios from 'axios';
import './jquery.hislide';

const {Stitch,RemoteMongoClient,AnonymousCredential} = require('mongodb-stitch-browser-sdk');

	/************* Start section for audio upload and view ************/			

export class Sync_facebook_data_you_profile extends React.Component {
	componentDidMount (){
				 $('.dropify').dropify();
				 $('.image-rotator').hiSlide();
				 
		}
		componentWillMount (){
				 $('.dropify').dropify();
				 $('.image-rotator').hiSlide();
		}
		componentUpdateMount (){
				 $('.dropify').dropify();
				 $('.image-rotator').hiSlide();
		}
	render () { 
		return (
		<div className="col-md-4 col-sm-4">
				<div className="profile-img-upload">
					<div className="profile-img-upload-inner">
						<input accept="image/*"  type="file" name="image" id="input-file-now" className="dropify"   />
					</div>
				</div>
			</div>
		)
	}
}

	/************* End section for audio upload and view ************/	

	

