let display_loader=STYLE.infoDisplayNone;
export class Fans_in_gender extends React.Component{
	constructor () {
		super();
		this.state={
		}
	}
	
	get_count_fans_with_gender = () => {
		/*	
		const page_data=JSON.parse(localStorage.getItem('page'));
		const selectedPage_data=JSON.parse(localStorage.getItem('selectedPage'));
		const page_id=page_data.accounts.data[selectedPage_data].id;
		const access_token=page_data.accounts.data[selectedPage_data].access_token;	
		axios.get(url+page_id+"/insights/page_fans_gender_age/?access_token="+access_token).then(response => {
			localStorage.setItem('fans_by_gender',JSON.stringify(response.data));
			display_loader=STYLE.infoDisplayNone;
		})
		*/
		if(localStorage.getItem('fans_by_gender')!==null || localStorage.getItem('fans_by_gender')!==undefined){
			const fans_by_gender=JSON.parse(localStorage.getItem('fans_by_gender'));
			if(fans_by_gender!==null){
				if(fans_by_gender.data.length>0){
					const Fans_data=fans_by_gender.data[0].values.map((item,key)=>{
						if(key==0){
							let item_value=item.value;
							var result = Object.keys(item_value).map(function(key) {
								  if(key.includes('F')){
									  female_data.push(item_value[key]);
								  } else if(key.includes('M')){
									  male_data.push(item_value[key]);
								  }
							});
						}
					});
				}
			}
		}
		
		for(var i=0;i<=female_data.length;i++){
			sum_of_female_data=parseInt(sum_of_female_data)+parseInt(female_data);
		}
		
		
		for(var i=0;i<=male_data.length;i++){
			sum_of_male_data=parseInt(sum_of_male_data)+parseInt(male_data);
		}
		total_sum=sum_of_male_data+sum_of_female_data;
		const data = {
						datasets: [{
										data: [
												(isNaN(sum_of_female_data)===false) ? (sum_of_female_data*100/total_sum) : 0,(isNaN(sum_of_male_data)===false) ? (sum_of_male_data*100/total_sum) : 100 
											  ],
										backgroundColor: [
												'#e9ae20',
												'#fff'
											 ]
									}]
		};
		
		return (	<div>
						<div className="col-md-8 col-sm-8 col-xs-12">
									<div className="pl-flow-chart">
										<Pie data={data} />
									</div>
								</div>
								<div className="col-md-12 col-sm-12 col-xs-12 rel-txt">
									<div className="footer-cont1">
									<br/>
										<p>{(isNaN((sum_of_female_data*100/total_sum).toFixed(2))) ? 0 :  (sum_of_female_data*100/total_sum).toFixed(2)} % Female	<br/>
										{(isNaN((sum_of_male_data*100/total_sum).toFixed(2))) ? 0 :  (sum_of_male_data*100/total_sum).toFixed(2)} % Male</p>
									</div>
								</div>
					</div>
					
				)
				
	}
	
	render() {
		return (<div>
						<div className="col-md-5  col-sm-5 col-xs-6">
							<div className="playit-chart">
								<div className="pl-title" >
									<h4>Fans</h4>
								</div>
								<div className="row">
									<div className="col-md-12">
										<div className=" pl-details3">
											<div className="col-md-4 col-sm-4 col-xs-12">
												<div className="pl-details-hding">
													<strong>Gender</strong>
												</div>
											</div>
										{this.get_count_fans_with_gender()}
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			);
	}
}


let english_lang=[];
let others_lang=[];
let total_lang_sum=0;
let sum_of_english_lang=0;
let sum_of_others_lang=0;

export class Page_language_data extends React.Component {
	get_language_data = () => {
		/*const page_data=JSON.parse(localStorage.getItem('page'));
		const selectedPage_data=JSON.parse(localStorage.getItem('selectedPage'));
		const page_id=page_data.accounts.data[selectedPage_data].id;
		const access_token=page_data.accounts.data[selectedPage_data].access_token;
		axios.get(url+page_id+"/insights/page_fans_locale/?access_token="+access_token).then(response => {
			localStorage.setItem('fans_by_language',JSON.stringify(response.data));
		})*/
		if(localStorage.getItem('fans_by_language')!==null || localStorage.getItem('fans_by_language')!==undefined){
			const fans_by_language=JSON.parse(localStorage.getItem('fans_by_language'));
			if(fans_by_language!==null){
				if(fans_by_language.data.length>0){
					const Fans_data=fans_by_language.data[0].values.map((item,key)=>{
						if(key==0){
							let item_value=item.value;
							var result = Object.keys(item_value).map(function(key) {
								  if(key.includes('en')){
									  english_lang.push(item_value[key]);
								  } else {
									  others_lang.push(item_value[key]);
								  }
							});
						}
					});
				}
			}
		}
		
		for(var i=0;i<english_lang.length;i++){
			sum_of_english_lang=parseInt(sum_of_english_lang)+parseInt(english_lang[i]);
		}
		
		
		for(var i=0;i<others_lang.length;i++){
			sum_of_others_lang=parseInt(sum_of_others_lang)+parseInt(others_lang[i]);
		}
		total_lang_sum=sum_of_english_lang+sum_of_others_lang;
		const data = {
			datasets: [{
						data: [
								(sum_of_english_lang!==0) ? (sum_of_english_lang*100/total_lang_sum) : 0,(sum_of_others_lang!==0) ? (sum_of_others_lang*100/total_lang_sum) : 100 
							  ],
						backgroundColor: [
								'#e9ae20',
								'#fff'
							 ]
					}]
		};
			return (<div>
						<div className="col-md-8 col-md-offset-left-2 right-img">
							<div className="pl-flow-chart">
								<Doughnut data={data} />
							</div>
						</div>
						<div className="col-md-12 col-sm-12 col-xs-12 rel-txt">
							<div className="footer-cont1">
								<p>{(isNaN((sum_of_english_lang*100/total_lang_sum).toFixed(2))) ? 0 :  (sum_of_english_lang*100/total_lang_sum).toFixed(2)} % English	<br/>
										{(isNaN((sum_of_others_lang*100/total_lang_sum).toFixed(2))) ? 0 :  (sum_of_others_lang*100/total_lang_sum).toFixed(2)} % Others
								</p>
							</div>
						</div>
					</div>
					)
		
	}
	render () {
		
		return (
			<div>
				<div className="col-md-5  col-sm-5 col-xs-6">
					<div className="playit-chart">
						<div className="pl-title">
							
						</div>
						<div className="row">
							<div className="col-md-12">
								<div className=" pl-details5">
									<div className="col-md-12 col-sm-12 col-xs-12">
										<div className="pl-details-hding">
											<strong>Launguage</strong>
										</div>
									</div>
									{this.get_language_data()}
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export class View_fan_location extends React.Component{
	get_Google_map () {
		const google=window.google;
		var country_code=new Array();
		var page=JSON.parse(localStorage.getItem('page'));
		var selectedPage=JSON.parse(localStorage.getItem('selectedPage'));
		if(page!==null && selectedPage!=null) {
			var selected_page_id=page.accounts.data[selectedPage].id;
			var selected_page_access_token=page.accounts.data[selectedPage].access_token;
			var fan_country_location=JSON.parse(localStorage.getItem('fan_country_location'));
			if(fan_country_location.data.data.length>0) {
				var obj=fan_country_location.data.data[0].values[0].value;
				var result = Object.keys(obj).map(function(key) {
					country_code=[(key), obj[key]];
				});
				
				var country=[['Country', 'Fans']];
				google.charts.load('current', {
					'packages':['geochart'],
					'mapsApiKey': 'AIzaSyD-9tSrke72PouQMnMX-a7eZSW0jkFMBWY'
				});
				google.charts.setOnLoadCallback(drawRegionsMap);
				function drawRegionsMap() {
					var data = google.visualization.arrayToDataTable(country.concat(Object.entries(obj)));

					var options = {
						tooltip: {textStyle: {color: '#FF0000'}, showColorCode: true},
						backgroundColor: '#141414',
						datalessRegionColor: '#e9ae20',
						defaultColor: '#e9ae20',
						colorAxis: {colors: ['#fff', '#fff']},
						width:'210'
					};
					var chart = new google.visualization.GeoChart(document.getElementById('regions_div'));
					chart.draw(data, options);
				}
			} else {
				google.charts.load('current', {
					'packages':['geochart'],
					'mapsApiKey': 'AIzaSyD-9tSrke72PouQMnMX-a7eZSW0jkFMBWY'
				});
				google.charts.setOnLoadCallback(drawRegionsMap);
			 function drawRegionsMap() {
					var data = google.visualization.arrayToDataTable([
					  ['Country', 'Fans'],
					  ['United States', 0]
					]);

					var options = {
						tooltip: {textStyle: {color: '#FF0000'}, showColorCode: true},
						backgroundColor: '#141414',
						datalessRegionColor: '#e9ae20',
						defaultColor: '#e9ae20',
						colorAxis: {colors: ['#fff', '#fff']},
						width:'210'
					};
					var chart = new google.visualization.GeoChart(document.getElementById('regions_div'));
					chart.draw(data, options);
				}
		}
		} 
	}
	render(){
		{this.get_Google_map()}
			return (
				<div id="regions_div" ></div>
			)
		}
}
let reverse='';
export class Page_growth extends React.Component{
	get_page_growth(){
		const page_growth_data=JSON.parse(localStorage.getItem('page_growth'));
		let growth_data=[];
		if(page_growth_data.data.data.length>0){
			const growth_value_data=page_growth_data.data.data.map((item,key)=>{
					growth_data[key]=item.values[0].value;
			})
			var mySet = new Set(growth_data);
			reverse = [...mySet].reverse();
				
			const data = {
					  labels: ['2017', '2018', '2019'],
					  datasets: [
								{
								  label: 'Growth',
								  fill: true,
								  lineTension: 0.1,
								  backgroundColor: '#e9ae20',
								  borderColor: '#e9ae20',
								  borderCapStyle: 'butt',
								  borderDash: [],
								  borderDashOffset: 0.0,
								  borderJoinStyle: 'miter',
								  pointBorderColor: '#e9ae20',
								  pointBackgroundColor: '#fff',
								  pointBorderWidth: 1,
								  pointHoverRadius: 5,
								  pointHoverBackgroundColor: '#e9ae20',
								  pointHoverBorderColor: '#e9ae20',
								  pointHoverBorderWidth: 1,
								  pointRadius: 1,
								  pointHitRadius: 10,
								  data: reverse
								}
							  ]
					};
					return (<div className="col-md-9 col-sm-9 col-xs-12">
								<div className="pl-flow-chart">
									<Line data={data} />
								</div>
							</div>
							
							);
					
		}
	}
	render(){
		
		return (
			<div className="row">
				
				<div className="col-md-3 col-sm-3 col-xs-12">
					<div className="pl-details-hding">
						<strong>Growth</strong>
					</div>
				</div>
				{this.get_page_growth()}
				<div className="col-md-12 col-sm-12 col-xs-12 rel-txt1">
					<div className="footer-cont1">
						<p>{reverse[0]}%</p>
					</div>
				</div>
			</div>
		)
	}
}

