import React, { Component } from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import $ from 'jquery';
var OAuth = require('oauth');
export let token='';

const access_token_url="/oauth/v2/accessToken";
let searchParams = new URLSearchParams(window.location.search);
	if(searchParams.has('code')){
		let searchParams = new URLSearchParams(window.location.search)
		token = searchParams.get('code');
		localStorage.setItem('linkedin_code',token);
			
		/*getting access_token*/
		
		const body = new URLSearchParams([
		  ['grant_type', 'authorization_code'],
		  ['code', token],
		  ['redirect_uri', window.location.origin+'/linkedin'],
		  ['client_id', '86k2973z1d6j20'],
		  ['client_secret', '8LUyYQZqOmOAVsF3']
		]);
		const headers = new Headers({'Content-Type': 'x-www-form-urlencoded'}); 
		axios.post(access_token_url,body,headers).then(result=>{
			console.log(result)
			localStorage.setItem('linkedin_access_token',JSON.stringify(result.data));
			if(localStorage.getItem('linkedin_access_token')!==null){
				const data=JSON.parse(localStorage.getItem('linkedin_access_token'));
				
				axios('/v2/me?projection=(id,firstName,lastName,profilePicture(displayImage~:playableStreams))&oauth2_access_token='+data.access_token).then(response=>{
					localStorage.setItem('linkedin_profile_data',JSON.stringify(response.data));
					window.close();
				})
			}
			
		}).catch(error=>{
			console.log(error);
		})
	}
export class LinkedIn extends Component {
  static propTypes = {
		className: PropTypes.string,
		onFailure: PropTypes.func.isRequired,
		onSuccess: PropTypes.func.isRequired,
		onClick: PropTypes.func,
		disabled: PropTypes.bool,
		clientId: PropTypes.string.isRequired,
		redirectUri: PropTypes.string.isRequired,
	}
	
  getUrl = () => {
    const {redirectUri, clientId, state, scope} = this.props;
    // TODO: Support IE 11
    const scopeParam = (scope) ? `&scope=${encodeURI(scope)}` : ''
    const linkedInAuthenLink = `https://www.linkedin.com/oauth/v2/authorization?response_type=code&client_id=${clientId}&redirect_uri=${redirectUri}${scopeParam}&state=${state}`;
	return linkedInAuthenLink;
  }

  receiveMessage = (event) => {
	  console.log(event.origin);
    if (event.origin === window.location.origin) {
      console.log('event data:', event.data);
      if (event.data.errorMessage && event.data.from === 'Linked In') {
        this.props.onFailure(event.data);
        this.popup && this.popup.close();
      } else if (event.data.code && event.data.from === 'Linked In') {
        this.props.onSuccess({ code: event.data.code });
        this.popup && this.popup.close();
      }
    }
  };

  handleConnectLinkedInClick = (e) => {
    if (e) {
      e.preventDefault();
    }
	console.log(this.getUrl());
    this.props.onClick && this.props.onClick();
    this.popup = window.open(this.getUrl(), '_blank', 'width=600,height=600');
	window.removeEventListener('message', this.receiveMessage, false);
    window.addEventListener('message', this.receiveMessage, false);
  }


  render() {
    const { className, disabled, children } = this.props;
    return (
      <button
        type="button"
        onClick={this.handleConnectLinkedInClick}
        className={className}
        disabled={disabled}
      >
        {children}
      </button>

    );
  }
}

LinkedIn.defaultProps = {
  className: 'btn-linkedin',
  disabled: false,
  children: ("Log in with Linked In"),
  state: 'fdsf78fyds7fm',
};
export default LinkedIn;
