import React, { Component } from 'react';
import {Header,client,db} from "../Header/Header.js";
import Calendar from 'react-calendar';
import { BrowserRouter, Route, NavLink, HashRouter, Switch,Redirect  } from "react-router-dom";
import graph from 'fb-react-sdk';
import {Sync_facebook_data_you_profile} from './slider_images_upload';
import $ from 'jquery';
import {mongodb_url,header_config} from '../../config/Index.jsx';
import axios from 'axios';

const user=JSON.parse(localStorage.getItem('user'));
const page=JSON.parse(localStorage.getItem('page'));
const selectedPage=JSON.parse(localStorage.getItem('selectedPage'));
export class Play_data extends React.Component {
	
	constructor () {
		super();
		this.state={
				play_tech_rider:''
		}
	}
	
	
	handleChange = (ev) => {
		//this.setState({play_tech_rider:ev.target.value});
		
	}
	play_list_data = () => {
			let list_data='';
			if(localStorage.getItem('players_data')!==null){
				const data=JSON.parse(localStorage.getItem('players_data'));
				if(data!==null){
					if(data!==undefined){
					list_data=data.map((items,key)=>{
							const item=JSON.parse(items.play_data);
							const total_price=parseInt(item.price_1)+parseInt(item.price_2)+parseInt(item.price_3);
							const total_audience=(parseInt(item.crown_size1[1])-parseInt(item.crown_size1[0]))+(parseInt(item.crown_size2[1])-parseInt(item.crown_size2[0]))+(parseInt(item.crown_size3[1])-parseInt(item.crown_size3[0]));
							return (
								<div className="item" key={key}>
								<div className="row">
									<div className="cutom-new-tabs-profile mob-profile">
											<ul>
												<li><NavLink to="#">{item.player_name}</NavLink></li>
											</ul>
										</div>
							 </div>
														<div className="play-about3">
															<div className="play-img1">
																<img src={(item.play_images!==undefined) ? item.play_images[0] : ""} alt="no-image" className="p-img1"/>
															</div>
															<div className="play-text1">
																<div className="pt-heading">
																	<h2 className="av-heading">{item.player_name}</h2>
																	<p className="av-title">{item.genre}</p>
																</div>
																<div className="pt-loc1">
																<ul>
																	<li>
																		<img src="images/time-watch-icon-white.png" className="time"/>{item.quantity}
																							
																	</li>
																	<li>
																		<img src="images/doler-icon-white.png" className="money"/>$ { total_price }
																							
																	</li>
																	<li>
																		<img src="images/group.png" className="grp"/>{total_audience}
																							
																	</li>
																	<li>
																		<div className="dbl-down-icon">
																			<i className="fa fa-angle-double-down" aria-hidden="true"></i>
																		</div>
																	</li>
																</ul>
															</div>
															<div className="pt-text1">
																<p><textarea onChange={this.handleChange} className="play_textarea" name="play_tech_rider" value={(this.state.play_tech_rider!=='') ? this.state.play_tech_rider : item.play_tech_rider}></textarea></p>
															</div>
															<div className="more-btn">
																<NavLink to={{pathname:"/Edit_Plays_data",data:key}} aria-current="page" className="active" href="">more&gt;&gt;</NavLink>
															</div>
															<div className="img-it-more-btn"></div>
														</div>
													</div>
												</div>
											
							)
						})
					}
				} 
			} 
			return list_data;
	}
	
	
	render(){
		const data=JSON.parse(localStorage.getItem('players_data'));
		return (
			<div>
							<div id="Plays-owl" className="owl-carousel owl-theme">
								{this.play_list_data()}
							</div>
					
					
		</div>
		)
	}
}

