import React, { Component } from 'react'
import Autocomplete from '@celebryts/react-autocomplete-tags'
import {tags} from '../../config/Index.jsx';

const onboarding_data=JSON.parse(localStorage.getItem('onboarding_data'));
export class You_tech_rider extends Component {

  	constructor(props){
	    super(props)
	    this.state = {
	      suggestions: tags,
		  you_tech_rider: []
	    }
  	}
  
  	componentWillMount (){
		if(localStorage.getItem('onboarding_data')===true){
			console.log('hello');
			sessionStorage.setItem('you_tech_rider',this.state.you_tech_rider);
		} else {
			
			if(onboarding_data!==null ){
				console.log(onboarding_data.you_tech_rider);
				if(onboarding_data.you_tech_rider!==undefined && onboarding_data.you_tech_rider!==''){
					this.state.you_tech_rider =("you_tech_rider" in onboarding_data) ? ((onboarding_data.you_tech_rider!==null) ? JSON.parse(onboarding_data.you_tech_rider) : this.state.you_tech_rider ) : this.state.you_tech_rider;
					
					
					this.state.suggestions =("you_tech_rider" in onboarding_data) ? ((onboarding_data.you_tech_rider!=='') ? JSON.parse(onboarding_data.you_tech_rider) : this.state.you_tech_rider ) : this.state.suggestions;
					
					sessionStorage.setItem('you_tech_rider',("you_tech_rider" in onboarding_data) ? ((onboarding_data.you_tech_rider!=='') ? onboarding_data.you_tech_rider : this.state.you_tech_rider ) : JSON.stringify(this.state.you_tech_rider));
				} else {
					sessionStorage.setItem('you_tech_rider',this.state.you_tech_rider);
				}
			}
		}
		
	}

  	handleAdd = (value) => {
   	 	const you_tech_rider = [].concat(this.state.you_tech_rider, value)
		sessionStorage.setItem('you_tech_rider',JSON.stringify(you_tech_rider));
  	}
  
  	handleDelete = (i) => {
  		const you_tech_rider = this.state.you_tech_rider.slice(0)
		you_tech_rider.splice(i, 1)
		sessionStorage.setItem('you_tech_rider',JSON.stringify(you_tech_rider));
  	}
  

  	render(){
	    return (
	      <Autocomplete 
			allowCreateTag={true}
			limitTags={40}
			tags={this.state.you_tech_rider}
	        suggestions={this.state.suggestions}
	        onAdd={this.handleAdd}
			onDelete={this.handleDelete}
			className="you_tech_rider"
	      />
    	)
  	}
}


