import React, { Component } from 'react';
import {  Route, NavLink, HashRouter, Switch,Redirect  } from "react-router-dom";
import { isMobile,isBrowser } from 'mobile-device-detect';
import './check_session.js';

const {Stitch,RemoteMongoClient,AnonymousCredential} = require('mongodb-stitch-browser-sdk');
const STYLE = {
    infoWidth: {
        width: '100%'
    },
};

if(sessionStorage.getItem('login_user_type')===null){
	sessionStorage.setItem('login_user_type','players');
}

/*if(sessionStorage.getItem('check_session')===null) {
	sessionStorage.setItem('check_session','false');
	window.location.href="/";
}*/

export const page_data=JSON.parse(localStorage.getItem('page'));
export const user_data=JSON.parse(localStorage.getItem('user'));
export const selectedPage_data=JSON.parse(localStorage.getItem('selectedPage'));
export const client = Stitch.initializeDefaultAppClient('playitlive-trism');
export const db = client.getServiceClient(RemoteMongoClient.factory, 'mongodb-atlas').db('PlayitLive');
export class Header extends Component {


	constructor(props) {
		super(props);
		this.state = {
			checked: false,
			orig:'',
			login_user_type:'players',
		}
	}
	isChecked = () => {
		this.setState({ checked: !this.state.checked })
	}
	componentWillMount() {
		if (localStorage.getItem("page")=== null && localStorage.getItem("user")=== null) {
			window.location.href="/";
		}
		if(localStorage.getItem('pages')){
			console.log(localStorage.getItem('pages'));
		}
		
	}
	componentDidMount() {
		this.state.orig = document.body.className;
		document.body.className =  window.location.pathname.replace('/',''); 
	}
	
	logout = () =>{
		if(localStorage.getItem('selectedPage')!==''){
			const user=JSON.parse(localStorage.getItem('user'));
			const page=JSON.parse(localStorage.getItem("page"));
			const selected_page_index=localStorage.getItem('selectedPage');
			if(sessionStorage.getItem('login_user_type')==='players'){
				localStorage.setItem('current_page',page.accounts.data[selected_page_index].id);
			}
			localStorage.removeItem('user');
			localStorage.removeItem('page');
			localStorage.removeItem('selectedPage');
		} else {
			localStorage.removeItem('user');
			localStorage.removeItem('page');
			localStorage.removeItem('selectedPage');
		}
		localStorage.removeItem('token');
		localStorage.removeItem('user_profile');
		localStorage.removeItem('selected_page_data');
		localStorage.removeItem('onboarding_data');
		localStorage.removeItem('fans_by_gender');
		localStorage.removeItem('fans_by_language');
		localStorage.removeItem('page_growth');
		localStorage.removeItem('players_data');
		localStorage.removeItem('fan_country_location');
		localStorage.removeItem('onboarding_play_data');
		sessionStorage.removeItem('login_user_type');
		//sessionStorage.removeItem('check_session');
		window.location.href="/";
	}
	
  render() {
	if (localStorage.getItem("page")!== null || localStorage.getItem("user")!== null || localStorage.getItem('selectedPage')!==null) {
	const page=JSON.parse(localStorage.getItem('page'));
	const user=JSON.parse(localStorage.getItem('user'));
	const selected_page_index = JSON.parse(localStorage.getItem('selectedPage')) ;
	return (
      <div>
		{(!isMobile) ? 
			(sessionStorage.getItem('login_user_type')!==null &&  sessionStorage.getItem('login_user_type')==="players") ?
		<section className="header-menu">
        <nav className="navbar navbar-inverse brand_nav">
            <div className="container Dashboard-brand">
                <div className="navbar-header">
                    <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                    </button>
                    <a className="navbar-brand custom-logo" ><img src="images/logo-icon.png"/></a>
              
                </div>
                <div className="collapse navbar-collapse" id="myNavbar">
                    <ul className="nav navbar-nav custim-navbar-nav-inner">
                        <li className="active"><NavLink to="/DashboardPlayers">Dashboard</NavLink></li>
                        <li><NavLink to="/Inbox">Inbox</NavLink></li>
                    </ul>
                    <ul className="nav navbar-nav navbar-right custim-navbar-nav-inner-1">
                        <li><a href="#">HELP</a></li>
                         <li className="custom-dropdown">
                        <a href="#" className="custom-dropdown-toggle profile-imgs" ><img src={page.accounts.data[selected_page_index].picture.data.url}/></a>
                        <ul className="custiom-dropdown-menu">
                           <li><a href="/Profile">profile</a></li>
                           <li><a onClick={this.logout} href='#'>Logout</a></li>
                        </ul>
                     </li>
                    </ul>
                </div>
            </div>
        </nav>
    </section> : <section className="header-menu">
        <nav className="navbar navbar-inverse brand_nav">
            <div className="container Dashboard-brand">
                <div className="navbar-header">
                    <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                    </button>
                    <a className="navbar-brand custom-logo" ><img src="images/logo-icon.png"/></a>
              
                </div>
                <div className="collapse navbar-collapse" id="myNavbar">
                    <ul className="nav navbar-nav custim-navbar-nav-inner">
                        <li className="active"><NavLink to="/DashboardHost">Dashboard</NavLink></li>
                        <li><NavLink to="/Host_Inbox">Inbox</NavLink></li>
						 <li><NavLink to="/Players">Players</NavLink></li>
                    </ul>
                    <ul className="nav navbar-nav navbar-right custim-navbar-nav-inner-1">
                        <li><a href="#">HELP</a></li>
                         <li className="custom-dropdown">
                        <a href="#" className="custom-dropdown-toggle profile-imgs" ><img src={page.accounts.data[selected_page_index].picture.data.url}/></a>
                        <ul className="custiom-dropdown-menu">
                           <li><NavLink to="/About">profile</NavLink></li>
                           <li><a href="#" onClick={this.logout}>Logout</a></li>
                        </ul>
                     </li>
                    </ul>
                </div>
            </div>
        </nav>
      
    </section>
	:
	<div className="pro-mobile-company">
         <section className="header-menu">
            <nav className="navbar navbar-inverse">
               <div className="container About_header">
                  <div className="navbar-header mob-nav-header">
                     <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#myNavbar1" aria-expanded="false">
                     <span className="icon-bar"></span>
                     <span className="icon-bar"></span>
                     <span className="icon-bar"></span>                        
                     </button>
                     <a className="navbar-brand custom-logo mobile-logo" href="#">
					 <img src="images/mobile-logo.png"/></a>
                     <div className="nw-icon">
                        <ul>
                           <li><a href="#"><img src="images/mo_icon3.png" alt="otr-menu1" className="other-menu1"/></a></li>
                           <li><a href="#"><img src="images/mo_icon2.png" alt="otr-menu1" className="other-menu2"/></a></li>
                           <li><a href="#"><img src="images/mo_icon1.png" alt="otr-menu1" className="other-menu2"/></a></li>
                        </ul>
                     </div>
                  </div>
                  <div className="navbar-collapse collapse" id="myNavbar1" aria-expanded="false">
                     <ul className="nav navbar-nav">
                        <li className="active"><NavLink to="/DashboardPlayers">Dashboard</NavLink></li>
                        <li><NavLink to="/Inbox">Inbox</NavLink></li>
                     </ul>
                     <ul className="nav navbar-nav navbar-right">
                        <li><a href="#">HELP</a></li>
                        <li className="user_image_header"><a href="/Profile"><span className="profile-img-menu">
						<img src={page.accounts.data[selected_page_index].picture.data.url}/></span></a></li>
						<li><a href="#" onClick={this.logout}>Logout</a></li>
                     </ul>
                  </div>
               </div>
            </nav>
         </section>
	  </div>
	}
	</div>
	
		);
	} else { return window.location.href="/"; }
	} 
  }
  
export default Header;