import React, {Component} from 'react';
import {Header,client,db} from "../Header/Header.js";
import S3FileUpload from 'react-s3';
import 'dropify';
import './dropify.min.css';
import $ from 'jquery';
import axios from 'axios';
import AudioPlayer from "react-h5-audio-player";
import { Player } from 'video-react';
import "./video-react.css";
import {mongodb_url,header_config} from '../../config/Index.jsx';
import {Image_upload} from '../UserDashboard/Image_upload.js';

const {Stitch,RemoteMongoClient,AnonymousCredential} = require('mongodb-stitch-browser-sdk');
const user=JSON.parse(localStorage.getItem('user'));
const page=JSON.parse(localStorage.getItem('page'));
const selectedPage=JSON.parse(localStorage.getItem('selectedPage'));
const onboarding_data=JSON.parse(localStorage.getItem('onboarding_data'));

export const STYLE = {
    infoDisplay: {
        display: 'block'
    },
    infoDisplayNone: {
        display: 'none'
    },
	infoColor:{
		color:"#e9ae20"
	},
	infoDisplayinline:{
		display: 'inline'
	},
	infoFloat :{
		'float':'right'
	},
	infoSyncDisabled:{
		'opacity': '0.9',
		'pointerEvents': 'none',
		'cursor': 'not-allowed'
	},
	infoSync:{
		'opacity': '1.0',
		'pointerEvents': 'auto',
	},
	infoDisplayInlineBlock:{
		'display':'inline-block',
	},
	infoEventDesign : {
		backgroundColor: "#e9ae20",
		borderRadius:'50px',
		padding:'10px'
	}
};

export const deleteFile = () => {
	var formData = new FormData();
	formData.append("url", 'https://playitmarkermediafiles.s3.amazonaws.com/playitlive/images/1559644922.png');
	const url="https://jamtechtechnologies.com/AWS/delete_files.php";
	axios.post(url, formData, {
		headers: {
			'Content-Type': 'multipart/form-data'
		}
	}).then(function(response){
		console.log(response);
	})
}

export const Loader =(display_loader)=> {
			return (
				<div className="loader" style={display_loader}>
					<img src="images/ezgif.com-video-to-gif.gif"/>
			    </div>
			)
}


export let loader_state=STYLE.infoDisplayNone;

	export class Sync_facebook_data extends React.Component {
			constructor () {
				super();
				this.state={
						 display_loader:STYLE.infoDisplayNone
					 }
			}
			componentDidMount (){
					 $('.dropify').dropify();
			}
			componentWillMount (){
					 $('.dropify').dropify();
			}
			componentUpdateMount (){
					 $('.dropify').dropify();
			}
			
		/********** Onboarding Page (Sync or upload your favourite media) Step  ************/		
			
			/************* Start Section for image upload and view ************/
		
			upload_you_section_file = (ev) => {
				var formData = new FormData();
				var imagefile = ev.target;
				formData.append("file", imagefile.files[0]);
				formData.append("folder_name", 'playitlive/images');
				formData.append("file_type", 'image');
				if(imagefile.files.length>0) {
					this.setState({display_loader:STYLE.infoDisplay})
					const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
					const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
					
					//let update_image_index=ev.currentTarget.dataset.tag;
					var update_image_index=[];
					if (extFile=="jpg" || extFile=="jpeg" || extFile=="png" || extFile=="gif"){
						//const url='https://jamtechtechnologies.com/AWS/aws_s3.php';
						const url="/aws_s3.php";
						   this.setState({
									display_loader:STYLE.infoDisplay
						   })
						   var self=this;
						axios.post(url, formData, {
							headers: {
								'Content-Type': 'multipart/form-data'
							}
						}).then(function(response){
							
							let data=[];
							let updated_image_url=response.data.trim();
							data.push(response.data.trim());
							if(response.data!==''){
								var formData = new FormData();
								formData.append("user_id", user.id);
								formData.append("page_id", page.accounts.data[selectedPage].id);
								axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
									update_image_index.push(("you_images" in result.data) ? result.data.you_images : "");
									 if(update_image_index.length>0){
										formData.append('you_images',data.concat(update_image_index));
									 } else {
										(result.data.you_images!=null) ?
										formData.append('you_images',data.concat(result.data.you_images)) : formData.append('you_images',data);
									 }
									axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
											self.setState({
													display_loader:STYLE.infoDisplayNone
											})
									})
								})
								
							}
							
						})
					} else {
						alert("Only jpg/jpeg, gif and png files are allowed!");
					}
				}
			}
			/******************* View  image Section***********************/
			
			render () {
				const facebook_images_data=JSON.parse(localStorage.getItem('facebook_pages_images'));
				const mongodb_data=JSON.parse(localStorage.getItem('onboarding_data'));
				var data=[];
				var images_data=("you_images" in mongodb_data) ? mongodb_data.you_images.split(',') : "";
				
				for(var key=0;key<4;key++) {
					data.push (
						<div key={key} className="add_img_block col-xs-6 cstm-plr">
							<input data-tag={key} accept="image/*" onChange={this.upload_you_section_file} type="file" name="image" id="input-file-now" className="dropify"  data-default-file={images_data[key]} />
						</div>
						)
				}
				return ( 	<div>{Loader(this.state.display_loader)}	
								{data}
							</div> 
						)
			}
		}
	/******************* End View  image Section***********************/

	/************* End  Section for image upload and view ************/			
	
	/************* Start section for video upload and view ************/			

	export class Sync_facebook_data_you_video extends React.Component {
		constructor () {
				super();
				this.state={
						 display_loader:STYLE.infoDisplayNone
					 }
		}
		upload_you_section_video = (ev) => {
				var formData = new FormData();
				var imagefile = ev.target;
				formData.append("file", imagefile.files[0]);
				formData.append("folder_name", 'playitlive/video');
				formData.append("file_type", 'video');
				if(imagefile.files.length>0) {
				  this.setState({display_loader:STYLE.infoDisplay})
				  const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
				  const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
				  var self=this;
				  if (extFile=="m4v" || extFile=="avi" || extFile=="mpg" || extFile=="mp4" || extFile=="mpeg" || extFile=="wmv"){
					  const url='https://jamtechtechnologies.com/AWS/aws_s3.php';
					axios.post(url, formData, {
						headers: {
						  'Content-Type': 'multipart/form-data'
						}
					}).then(function(response){
						
						let data=response.data.trim()
						
						if(response.data!==''){
								var formData = new FormData();
								formData.append("user_id", user.id);
								formData.append("page_id", page.accounts.data[selectedPage].id);
								formData.append('you_video',data);
								axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
									axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
										localStorage.setItem('onboarding_data',JSON.stringify(result));
										self.setState({
												display_loader:STYLE.infoDisplayNone
										})
									});
								});
						}
					})
				  } else {
					  alert("Only mpg/mpeg, mp4, avi, wmv and m4v files are allowed!");
				  }
					
				}
			}
			/******************* View Video Section***********************/
		render () { 
			const mongodb_data=JSON.parse(localStorage.getItem('onboarding_data'));
				if(mongodb_data!==null && mongodb_data.you_video!==undefined){
					return (<div className="add_img_block_size_2 mb-10">
							<Player
								  preload="auto"
								  playsInline	
								  src={mongodb_data.you_video}
							/> 
							</div>)
				} else {
						return (<div>{Loader(this.state.display_loader)}
								<div className="add_img_block_size_2 mb-10">
									<div className="add_img_block_2 col-xs-12 video_dropify">
										<input accept="video/*" onChange={this.upload_you_section_video} type="file" name="image" id="input-file-now" className="dropify"   />
									</div>
								</div>
								</div>
								)
						}
				}
		}
		/******************* End View Video Section***********************/

		/************* End section for video upload and view ************/			
	/************ Start section for audio upload and view ***********/			
		

	export class Sync_facebook_data_you_audio extends React.Component {
		constructor () {
				super();
				this.state={
						 display_loader:STYLE.infoDisplayNone
					 }
		}
		upload_you_section_audio = (ev) => {
		var formData = new FormData();
		var imagefile = ev.target;
		formData.append("file", imagefile.files[0]);
		formData.append("folder_name", 'playitlive/audio');
		formData.append("file_type", 'audio');
		if(imagefile.files.length>0) {
			var self=this;
			this.setState({display_loader:STYLE.infoDisplay})
			const idxDot = imagefile.files[0].name.lastIndexOf(".") + 1;
			const extFile = imagefile.files[0].name.substr(idxDot, imagefile.files[0].name.length).toLowerCase();
			if (extFile=="mp3" || extFile=="mp4"){
				const url='https://jamtechtechnologies.com/AWS/aws_s3.php';
				axios.post(url, formData, {
					headers: {
					  'Content-Type': 'multipart/form-data'
					}
				}).then(function(response){
					let data=response.data.trim()
					if(response.data!==''){
						var formData = new FormData();
								formData.append("user_id", user.id);
								formData.append("page_id", page.accounts.data[selectedPage].id);
								formData.append('you_audio',data);
								axios.post(mongodb_url+"onboarding_api",formData,header_config).then(result =>{
									axios.post(mongodb_url+"get_login_user_data",formData,header_config).then(result => {
										localStorage.setItem('onboarding_data',JSON.stringify(result));
										self.setState({
												display_loader:STYLE.infoDisplayNone
										})
									});
								});
					}
				})
			  } else {
				  alert("Only mp3 and mp4 files are allowed!");
			  }
			}
		}
		render () { 
			const mongodb_data=JSON.parse(localStorage.getItem('onboarding_data'));
			if(mongodb_data!==null && mongodb_data.you_audio!==undefined){
			return (<div className="add_img_block_size_3" preload="auto">
						<AudioPlayer src={mongodb_data.you_audio} />
				</div>)
			} else {
			return (<div>{Loader(this.state.display_loader)}
					<div className="add_img_block_size_3">
						<div className="add_img_block_3 col-xs-12 video_dropify audio_drfy">
							 <input accept="audio/*" onChange={this.upload_you_section_audio} type="file" name="image" id="input-file-now" className="dropify"   />
						</div>
					</div>
					</div>
				)
			}
		}
	}

		/************ End section for audio upload and view ***********/


