import React, { Component } from "react";
import { BrowserRouter, Route, NavLink, HashRouter, Switch } from "react-router-dom";
import './index.js';
import Home from "./Home/Home.js";
import AfterLogin from "./facebook_components/AfterLogin/AfterLogin.js";
import Onboarding from "./facebook_components/UserDashboard/onboarding.js";
import ArtistProfile from "./facebook_components/ArtistProfile/artistprofile.js";
import Host_Inbox from "./facebook_components/Host_Inbox/Host_Inbox.js";
import Inbox from "./facebook_components/Inbox/inbox.js";
import About from "./facebook_components/About/about.js";
import Players from "./facebook_components/Players/players.js";
import Profile from "./facebook_components/Profile/Profile.js";
import Edit_Plays_data from "./facebook_components/Profile/Edit_Plays_data.js";
import FeedSelectPlay from "./facebook_components/FeedSelectPlay/feedSelectPlay.js";
import Header from "./facebook_components/Header/Header.js";
import ServiceFeed from "./facebook_components/ServiceFeed/serviceFeed.js";
import ServiceFeedpage from "./facebook_components/ServiceFeedpage/ServiceFeedpage.js";
import DashboardPlayers from "./facebook_components/DashboardPlayers/DashboardPlayers.js";
import DashboardBrand from "./facebook_components/DashboardBrand/DashboardBrand.js";
import DashboardHost from "./facebook_components/DashboardHost/DashboardHost.js";
import HostProfileaRtistSide from "./facebook_components/HostProfileaRtistSide/HostProfileaRtistSide.js";
import Onboarding_host from "./facebook_components/Onboarding_host/Onboarding_host.js";
import Host_Profile from "./facebook_components/Host_Profile/Host_Profile.js";

import Testing_page from "./Testing_page/Testing_page.js";

class Main extends Component {
render() {
return (
<BrowserRouter>
<div>
<div className="content">

<Route exact path="/" component={Home}/>
<Route exact path="/AfterLogin" component ={AfterLogin}/>
<Route exact path="/Onboarding" component ={Onboarding}/>
<Route exact path="/ArtistProfile" component ={ArtistProfile}/>
<Route exact path="/Host_Inbox" component ={Host_Inbox}/>
<Route exact path="/Inbox" component ={Inbox}/>
<Route exact path="/About" component ={About}/>
<Route exact path="/Players" component ={Players}/>
<Route exact path="/Profile" component={Profile}/>
<Route exact path="/FeedSelectPlay" component={FeedSelectPlay}/>
<Route exact path="/Header" component={Header}/>
<Route exact path="/ServiceFeed" component={ServiceFeed}/>
<Route exact path="/ServiceFeedpage" component={ServiceFeedpage}/>
<Route exact path="/DashboardPlayers" component={DashboardPlayers}/>
<Route exact path="/DashboardBrand" component={DashboardBrand}/>
<Route exact path="/DashboardHost" component={DashboardHost}/>
<Route exact path="/HostProfileaRtistSide" component={HostProfileaRtistSide}/>
<Route exact path="/Onboarding_host" component={Onboarding_host}/>
<Route exact path="/Host_Profile" component={Host_Profile}/>
<Route exact path="/Edit_Plays_data" component={Edit_Plays_data}/>

<Route exact path="/Testing_page" component={Testing_page}/>

</div>
</div>
</BrowserRouter>

);
}
}

export default Main;