let play_tag='';
const handle = (props) => {
	const { value, dragging, index, ...restProps } = props;
	return (
		<Tooltip
			prefixCls="rc-slider-tooltip"
			overlay={value}
			visible={dragging}
			placement="top"
			key={index}
		>
			<Handle value={value} {...restProps} />
		</Tooltip>
	);
};

class Play_tags extends React.Component{
	constructor (){
		super();
		this.state={
			play_tags: [
						{ label: "Public Figure", name: "Public Figure" },
					  ],
			suggestions: tags,
		}
	}
	
	
	handleDelete = (i) => {
		const play_tags = this.state.play_tags.slice(0)
		play_tags.splice(i, 1)
		const play_tag=[].concat(this.state.play_tags, play_tags)
	}
	
	handleAdd = (value) => {
		const play_tags = [].concat(this.state.play_tags, value)
    }
	
	render(){
		return (
			<Autocomplete 
				allowCreateTag={true}
				limitTags={40}
				tags={this.state.play_tags}
				suggestions={this.state.suggestions}
				onAdd={this.handleAdd}
				onDelete={this.handleDelete}
				className="play_tags"
		    />
		)
	}
}

