	check_player_name = (value,id) => {
	  	if(localStorage.getItem('players_data')!==''){
	  		var players_data=JSON.parse(localStorage.getItem('players_data'));
	  		players_data.map((items,key)=>{
	  			let data=JSON.parse(items.play_data);
				if(data.player_name===value){
					Swal.fire('Info', 'Player name already exist Please try something else', 'info');
					$('#'+id).val('');
					$('#'+id).focus();
					this.setState({hide_show_section:STYLE.infoDisplayNone}); 
				} 
	  		})
	  	}
	}

	player_name = (e) => {
		if(e.target.value===''){
			this.setState({hide_show_section:STYLE.infoDisplayNone}); 
		} else {
			this.check_player_name(e.target.value,e.target.id);
		}
	}

